import { Checkers } from './checkers.js';
import { CheckersPlayer } from './checkersPlayer.js';
import { store } from './store.js';

const rules = `Checkers Rules
Movement
• Pawns: one square diagonally forward only
• Kings: one square diagonally any direction
• Black advances toward row 8
• Red advances toward row 1
Captures
• Mandatory when available
• Jump over enemy to empty square beyond
• Pawns capture forward only
• Kings capture any direction
• Multiple jumps required if possible
Kings
• Pawn promotes upon reaching opposite end
• Black kings at row 8; Red kings at row 1
Notation
• Pieces: r for red, b for black, capitalized for Kings
• Columns A-H, rows 1-8
• Moves: "A3-B4"
• Captures: "A3xC5"
• Multiple captures: "A1xC3xE5"
Winning
• Win by capturing all pieces or blocking all opponent moves
\n`;

/**
 * @type {(board: Object, players: Array<CheckersPlayer>) => Promise<Object>} play
 */
export async function play(board, players) {
    const match = new Checkers(players[0].side === 'black');
    const history = [];
    const minTurnMs = 2500;
    let start = 0;
    let retries = 3;
    let retryMessage = '';
    try {
        for (let turn = 0; ; turn++) {
            if (Date.now() - start < minTurnMs) {
                await new Promise(resolve => setTimeout(resolve, minTurnMs - (Date.now() - start)));
            }

            // hold for any user pauses
            await board.wait;
            start = Date.now();
            const side = turn % 2;
            const player = players[side];
            const color = player.side;
            const moves = match.generateAllMoves().map(move => match.formatMove(move));
            const responseSchema = player.getMoveSchema(moves);
            const capturing = moves.some(move => move.includes('x'));
            if (!capturing) {
                delete responseSchema.schema.properties.captureFlair;
                responseSchema.schema.required.pop();
            }
            const messageRules = turn < 2 ? rules : '';
            // const messageRules = rules;
            const capturingFlairMessages = `Captures will be done with the flair of your choosing, including flips and spirals with double, triples, and back flips. Choose your flair ahead.`;
            const message = `${messageRules}${retryMessage}${match.printBoard()}\n\nYour pieces are ${color} and it's your turn to move. Feel free to trash talk your opponent, use emojis, etc. ${capturing ? capturingFlairMessages : ''} Choose from these moves: ${moves}`;
            const thoughtPartsCallback = (chunk, type) => board.streamThoughts(side, chunk, type);
            const response = await player.sendMessage(responseSchema, message, thoughtPartsCallback);
            await board.wait;
            const responseContent = response.content;
            history.push({
                date: toLocalISODateTimeString(new Date()),
                turn,
                color,
                schema: responseSchema,
                message,
                response: responseContent,
            });
            const move = responseContent.move.toUpperCase().replace(/X/g, 'x');
            const captureFlair = responseContent.captureFlair;
            if (responseSchema.schema.properties.move.enum.includes(move)) {
                retries = 3;
                retryMessage = '';
                match.applyMove(move);
                await board.showThoughts(side, responseContent.comment);
                await board.movePiece(move, captureFlair);

                if (match.isGameOver()) {
                    return {
                        winner: color,
                        history,
                        board: match.printBoard(),
                    };
                }
            } else {
                turn--;
                retries--;
                if (retries === 0) {
                    return {
                        winner: players[(side + 1) % 2].side,
                        history,
                        board: match.printBoard(),
                    };
                }
                retryMessage = `Invalid move: "${move}". Please try again.\n`;
            }
        }
    } catch (error) {
        console.error(error);
        return {
            winner: 'Error',
            history,
            board: match.printBoard(),
            error: error.message,
        };
    }
}

const toLocalISODateTimeString = date =>
    new Date(date.getTime() - date.getTimezoneOffset() * 60 * 1000).toISOString().slice(0, -1);
