import { regex } from './regex.js';

/**
 * @typedef {EventInit & { value?: any, path?: string, error?: Error }} JsonStreamEventInit
 */
class JsonStreamEvent extends Event {
    /**
     * @param {string} type
     * @param {JsonStreamEventInit} options
     */
    constructor(type, options) {
        super(type, options);
        this.value = options?.value;
        this.path = options?.path;
        this.error = options?.error;
    }
}

// import { ParseError } from './parseError.js';
class ParseError extends Error {
    /**
     * @param {string} message
     * @param {{ cause?: Error, offset?: number, line?: number, column?: number, path?: string }} options
     */
    constructor(message, { cause, offset, line, column, path }) {
        super(message, { cause });
        this.offset = offset;
        this.line = line;
        this.column = column;
        this.path = path;
    }

    [Symbol.for('nodejs.util.inspect.custom')](depth, options, inspect) {
        return `${this.path ?? ''}:${this.line ?? ''}:${this.column ?? ''}:${this.offset ?? ''} ${this.message}`;
    }
}

// const regex =
//     /(?<objectStart>\{)|(?<objectEnd>\})|(?<arrayStart>\[)|(?<arrayEnd>\])|(?<string>"(?:[^"\\]|\\.)*(?:"|$))|(?<number>-?(?:0|[1-9]\d*)(?:\.\d+)?(?:[eE][+-]?\d+)?)|(?<boolean>true|false)|(?<null>null)|(?<colon>:)|(?<comma>,)|(?<whitespace>\s+)|(?<comment>\/\/[^\n]*|\/\*.*?\*\/)/gs;

const jsonNumber = regex/* regexp */ `
    -?
    (?:0|[1-9]\d*)
    (?:\.\d+)?
    (?:[eE][+-]?\d+)?
`;

const jsonParts = regex/* regexp */ `
    (?<objectStart> \{ )                     |
    (?<objectEnd> \} )                       |
    (?<arrayStart> \[ )                      |
    (?<arrayEnd> \] )                        |
    (?<string> "(?:[^"\\]|\\.)*(?:"|$) )     |
    (?<number> ${jsonNumber} )               |
    (?<boolean> true | false)                |
    (?<null> null )                          |
    (?<colon> : )                            |
    (?<comma> , )                            |
    (?<whitespace> \s+ )                     |
    (?<comment> \/\/[^\n]*     |
                \/\*.*?\*\/)
    ${'/gs'}
`;

export class JsonStreamParser {
    /**
     * @param {{ allowComments?: boolean, allowTrailingCommas?: boolean, decoder?: TextDecoder }} [options]
     */
    constructor(options = {}) {
        this.allowComments = options.allowComments ?? false;
        this.allowTrailingCommas = options.allowTrailingCommas ?? true;
        this.decoder = options.decoder;
        this.parsing = false;
    }

    #listeners = { value: [], part: [], end: [], error: [] };
    addEventListener(eventType, listener) {
        if (eventType in this.#listeners) {
            this.#listeners[eventType].push(listener);
        }
    }
    removeEventListener(eventType, listener) {
        if (eventType in this.#listeners) {
            this.#listeners[eventType] = this.#listeners[eventType].filter(x => x !== listener);
        }
    }
    dispatchEvent(event) {
        this.#listeners[event.type]?.forEach(listener => listener(event));
    }

    push(chunk) {
        if (!this.parsing) {
            this.parse(this.streamGenerator());
        }
        this.streamBuffer.push(chunk);
        this.streamActivity?.();
    }

    streamBuffer = [];
    async *streamGenerator() {
        while (this.parsing) {
            if (this.streamBuffer.length === 0) {
                await new Promise(resolve => (this.streamActivity = resolve));
            }
            yield this.streamBuffer.shift();
        }
    }

    async parse(asyncIterator) {
        if (this.parsing) {
            throw new Error(`Already parsing ${this.parsing}`);
        }
        this.parsing = asyncIterator;
        let accumulator = '';
        let partialString = '';
        let state = 'init';
        const contexts = [];
        const states = [];
        const subscripts = [];
        let initValue;

        // character offset of every newline in the stream
        const newlineOffsets = [];
        // character offset of the current accumulator in the stream
        let accumulatorOffset = 0;
        try {
            for await (const chunk of asyncIterator) {
                if (chunk.length === 0) {
                    continue;
                }
                // character offset of the current token in the accumulator.
                // used to validate every token.index for complete coverage of stream with no gaps
                let tokenOffset = 0;
                accumulator += this.decoder ? this.decoder.decode(chunk, { stream: true }) : chunk;

                if (state === 'string') {
                    const stringParts = regex/*regexp*/ `
                        ^(?<stringPart>(?:[^"\\]|\\.)*)
                         (?<ending>"|\\?$)
                    `;
                    const [_, moreString, ending] = accumulator.match(stringParts) ?? [];
                    const { result, dangle } = this.escapeString(moreString);
                    if (dangle) {
                        // unicode escape sequence is incomplete or invalid
                        const error = new ParseError('Invalid escape sequence', {
                            offset: accumulatorOffset + moreString.length - dangle.length,
                        });
                        this.dispatchEvent(new JsonStreamEvent('error', { error }));
                        throw error;
                    }
                    const endingLen = ending === '"' ? 1 : 0;
                    accumulator = accumulator.slice(moreString.length + endingLen);
                    accumulatorOffset += moreString.length + endingLen;
                    tokenOffset = 0;
                    partialString += result;
                    const priorState = states.at(-1);
                    if (priorState !== 'object-key' && priorState !== 'object-key-opt') {
                        this.dispatchEvent(new JsonStreamEvent('part', { path: subscripts.join('.'), value: result }));
                    }
                    if (ending !== '"') {
                        continue;
                    }
                    state = states.pop();
                    const escapedPartialString = JSON.stringify(partialString);
                    const token = {
                        type: 'string',
                        value: partialString,
                        length: escapedPartialString.length,
                        index: accumulatorOffset - escapedPartialString.length,
                    };
                    if (state === 'array-element' || state === 'array-element-opt') {
                        state = this.handleArrayElementState({
                            token,
                            contexts,
                            subscripts,
                            state,
                            states,
                            accumulatorOffset,
                        });
                    } else if (state === 'object-key-opt' || state === 'object-key') {
                        state = this.handleObjectKeyState({
                            token,
                            subscripts,
                            state,
                            accumulatorOffset,
                            states,
                            contexts,
                        });
                    } else if (state === 'object-value') {
                        state = this.handleObjectValueState({
                            token,
                            contexts,
                            subscripts,
                            state,
                            states,
                            accumulatorOffset,
                        });
                    }

                    partialString = '';
                }
                for (const match of accumulator.matchAll(jsonParts)) {
                    const [[type, value]] = Object.entries(match.groups).filter(([_, v]) => v != null || v === null);
                    const token = { type, value, length: match[0].length, index: match.index };
                    if (tokenOffset !== token.index) {
                        this.dispatchEvent(new Event('error'));
                        throw new ParseError(
                            `Syntax error: ${token.type} ${accumulator.slice(tokenOffset, token.index)} `,
                            { offset: accumulatorOffset + tokenOffset },
                        );
                    }
                    if (
                        ['comment', 'number'].includes(token.type) &&
                        token.index + token.length === accumulator.length
                    ) {
                        // these tokens might continue in the next chunk
                        break;
                    }
                    if (token.type === 'string') {
                        if (token.value === '"' || token.value.at(-1) !== '"') {
                            break;
                        }
                        token.value = token.value.slice(1, -1);
                    }
                    if (token.type === 'whitespace' || token.type === 'comment') {
                        const offset = token.index + accumulatorOffset;
                        newlineOffsets.push(...[...token.value.matchAll(/\n/g)].map(match => match.index + offset));
                        if (token.type === 'whitespace' || this.allowComments) {
                            tokenOffset += token.length;
                            continue;
                        }
                    }
                    switch (state) {
                        case 'init':
                            ({ initValue, state } = this.handleInitState({
                                token,
                                initValue,
                                contexts,
                                states,
                                state,
                                accumulatorOffset,
                            }));
                            break;

                        case 'object-key':
                        case 'object-key-opt':
                            state = this.handleObjectKeyState({
                                token,
                                subscripts,
                                state,
                                accumulatorOffset,
                                states,
                                contexts,
                            });
                            break;

                        case 'object-colon':
                            state = this.handleObjectColonState({ token, state, accumulatorOffset });
                            break;

                        case 'object-value':
                            state = this.handleObjectValueState({
                                token,
                                contexts,
                                subscripts,
                                state,
                                states,
                                accumulatorOffset,
                            });
                            break;

                        case 'object-comma':
                            state = this.handleObjectCommaState({
                                token,
                                state,
                                states,
                                subscripts,
                                contexts,
                                accumulatorOffset,
                            });
                            break;

                        case 'array-element':
                        case 'array-element-opt':
                            state = this.handleArrayElementState({
                                contexts,
                                token,
                                subscripts,
                                state,
                                states,
                                accumulatorOffset,
                            });
                            break;

                        case 'array-comma':
                            state = this.handleArrayCommaState({
                                token,
                                state,
                                subscripts,
                                states,
                                contexts,
                                accumulatorOffset,
                            });
                    }
                    tokenOffset += token.length;
                }

                accumulatorOffset += tokenOffset;
                accumulator = accumulator.slice(tokenOffset);
                if (initValue && state === 'init') {
                    this.dispatchEvent(new JsonStreamEvent('end', { path: subscripts.join('.'), value: initValue }));
                    break;
                }

                if (accumulator[0] === '"') {
                    states.push(state);
                    state = 'string';
                    const { result, dangle } = this.escapeString(accumulator.slice(1));
                    partialString = result;
                    if (dangle) {
                        accumulatorOffset += accumulator.length - dangle.length;
                        accumulator = dangle;
                    } else {
                        accumulatorOffset += accumulator.length;
                        accumulator = '';
                    }
                    this.dispatchEvent(new JsonStreamEvent('part', { path: subscripts.join('.'), value: result }));
                }
            }
        } catch (e) {
            if (e instanceof ParseError) {
                const newlines = newlineOffsets.filter(offset => offset < e.offset);
                e.line = newlines.length + 1;
                e.column = e.offset - (newlines.at(-1) ?? -1);
            }
            throw e;
        } finally {
            this.parsing = false;
        }
        return initValue;
    }

    handleObjectColonState({ token, state, accumulatorOffset }) {
        if (token.type === 'colon') {
            state = 'object-value';
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleArrayCommaState({ token, state, subscripts, states, contexts, accumulatorOffset }) {
        if (token.type === 'comma') {
            state = 'array-element';
            subscripts[subscripts.length - 1] = subscripts.at(-1) + 1;
        } else if (token.type === 'arrayEnd') {
            subscripts.pop();
            state = states.pop();
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: contexts.pop() }));
            subscripts.pop();
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleObjectCommaState({ token, state, states, subscripts, contexts, accumulatorOffset }) {
        if (token.type === 'comma') {
            state = 'object-key';
        } else if (token.type === 'objectEnd') {
            state = states.pop();
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: contexts.pop() }));
            subscripts.pop();
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleArrayElementState({ contexts, token, subscripts, state, states, accumulatorOffset }) {
        let arr = /** @type Array<any> */ (contexts.at(-1));
        if (token.type === 'string') {
            const { result, dangle } = this.escapeString(token.value);
            if (dangle) {
                const error = new ParseError('Invalid escape sequence', { offset: accumulatorOffset + token.index });
                this.dispatchEvent(new JsonStreamEvent('error', { error }));
                throw error;
            }
            arr.push(result);
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: result }));
            state = 'array-comma';
        } else if (token.type === 'number') {
            arr.push(Number(token.value));
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: token.value }));
            state = 'array-comma';
        } else if (token.type === 'boolean') {
            arr.push(token.value === 'true');
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: token.value }));
            state = 'array-comma';
        } else if (token.type === 'null') {
            arr.push(null);
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: token.value }));
            state = 'array-comma';
        } else if (token.type === 'objectStart') {
            const val = {};
            arr.push(val);
            contexts.push(val);
            states.push('array-comma');
            state = 'object-key-opt';
        } else if (token.type === 'arrayStart') {
            const val = [];
            arr.push(val);
            contexts.push(arr);
            states.push('array-comma');
            state = 'array-element-opt';
        } else if (token.type === 'arrayEnd') {
            if (state === 'array-element' && !this.allowTrailingCommas) {
                const error = new ParseError('Unexpected comma token: ,', { offset: accumulatorOffset + token.index });
                this.dispatchEvent(new JsonStreamEvent('error', { error }));
                throw error;
            }
            contexts.pop();
            state = states.pop();
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: arr }));
            subscripts.pop();
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleObjectValueState({ token, contexts, subscripts, state, states, accumulatorOffset }) {
        if (token.type === 'string') {
            const { result, dangle } = this.escapeString(token.value);
            if (dangle) {
                const error = new ParseError('Invalid escape sequence', { offset: accumulatorOffset + token.index });
                this.dispatchEvent(new JsonStreamEvent('error', { error }));
                throw error;
            }
            contexts.at(-1)[subscripts.at(-1)] = result;
            state = 'object-comma';
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: result }));
            subscripts.pop();
        } else if (token.type === 'number') {
            contexts.at(-1)[subscripts.at(-1)] = Number(token.value);
            state = 'object-comma';
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: token.value }));
            subscripts.pop();
        } else if (token.type === 'boolean') {
            contexts.at(-1)[subscripts.at(-1)] = token.value === 'true';
            state = 'object-comma';
            this.dispatchEvent(
                new JsonStreamEvent('value', { path: subscripts.join('.'), value: token.value === 'true' }),
            );
            subscripts.pop();
        } else if (token.type === 'null') {
            contexts.at(-1)[subscripts.at(-1)] = null;
            state = 'object-comma';
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: null }));
            subscripts.pop();
        } else if (token.type === 'objectStart') {
            contexts.push((contexts.at(-1)[subscripts.at(-1)] = {}));
            states.push('object-comma');
            state = 'object-key-opt';
        } else if (token.type === 'arrayStart') {
            contexts.push((contexts.at(-1)[subscripts.at(-1)] = []));
            states.push('object-comma');
            state = 'array-element-opt';
            subscripts.push(0);
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleObjectKeyState({ token, subscripts, state, accumulatorOffset, states, contexts }) {
        if (token.type === 'string') {
            const { result, dangle } = this.escapeString(token.value);
            if (dangle) {
                const error = new ParseError('Invalid escape sequence', { offset: accumulatorOffset + token.index });
                this.dispatchEvent(new JsonStreamEvent('error', { error }));
                throw error;
            }
            subscripts.push(result);
            state = 'object-colon';
        } else if (token.type === 'objectEnd') {
            if (state === 'object-key' && !this.allowTrailingCommas) {
                const error = new ParseError('Unexpected comma token: ,', { offset: accumulatorOffset + token.index });
                this.dispatchEvent(new JsonStreamEvent('error', { error }));
                throw error;
            }
            subscripts.pop();
            state = states.pop();
            const object = contexts.pop();
            this.dispatchEvent(new JsonStreamEvent('value', { path: subscripts.join('.'), value: object }));
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return state;
    }

    handleInitState({ token, initValue, contexts, states, state, accumulatorOffset }) {
        if (token.type === 'objectStart') {
            initValue = {};
            contexts.push(initValue);
            states.push(state);
            state = 'object-key-opt';
        } else if (token.type === 'arrayStart') {
            initValue = [];
            contexts.push(initValue);
            states.push(state);
            state = 'array-element-opt';
        } else {
            const error = new ParseError(`Unexpected ${token.type} token: ${token.value} `, {
                offset: accumulatorOffset + token.index,
            });
            this.dispatchEvent(new JsonStreamEvent('error', { error }));
            throw error;
        }
        return { initValue, state };
    }

    escapeMap = {
        n: '\n',
        r: '\r',
        t: '\t',
        f: '\f',
        b: '\b',
        '/': '/',
        '"': '"',
        '\\': '\\',
    };

    escapeString(str) {
        const expectTrailingBackslash = str.slice(-2) === '\\\\';
        const result = str.replace(/\\u(....)|\\(.)/g, (_, u, c) => {
            if (u) {
                if (u.match(/^[0-9A-F]{4}$/i)) {
                    return String.fromCodePoint(parseInt(u, 16));
                }
                throw new ParseError('Invalid Unicode escape sequence', { offset: u.index });
            } else {
                return this.escapeMap[c] ?? c;
            }
        });
        const [dangle] = (!expectTrailingBackslash && result.slice(-5).match(/\\$|\\u[0-9a-fA-F]{0,3}$/)) || [];
        return { result: dangle ? result.slice(0, -dangle.length) : result, dangle };
    }
}

