/**
 * @typedef ServerSentEvent
 * @property {string} [data]    The event payload
 * @property {string} [event]   The event type
 * @property {string} [id]      The event ID
 *
 * @async @function parseEventStream
 * @param {ReadableStreamDefaultReader} reader
 * @param {TextDecoder} [decoder]
 * @returns {AsyncGenerator<ServerSentEvent>}
 *
 * @description A streaming parser of Server-Sent Event streams, yields a series of objects matching the ServerSideEvent type. Use as an async
 * iterator or object stream, like this:
 *
 * @example // construct with a byte stream reader + text decoder
 *          const byteReader = response.body.getReader();
 *          const decoder = new TextDecoder(charset);
 *          const eventStream = parseEventStream(byteReader, decoder);
 *
 *          // or with a character stream reader
 *          const charReader = response.body.pipeThrough(new TextDecoderStream(charset)).getReader();
 *          const eventStream = parseEventStream(charReader);
 *
 *          // now you can consume the stream like this:
 *          const {done, value} = await eventStream.next();
 *
 *          // or iterate over the stream like this:
 *          for await (const event of eventStream) {...}
 */
export async function* parseEventStream(reader, decoder) {
    try {
        let message;
        let accumulator = '';
        while (true) {
            const { done, value: chunk } = await reader.read();
            if (done) {
                // we should get a line of "data: [DONE]" but got end of stream instead
                throw new Error('Unexpected end of Server-Sent Event stream');
            }

            accumulator += decoder ? decoder.decode(chunk, { stream: true }) : chunk;

            while (accumulator.length > 0) {
                const [substring, comment, name, value = '', emptyLine] =
                    accumulator.match(
                        // each line of input is either a :comment, name:value (only name is required), or empty
                        /^(?::(?<comment>[^\n]*)|(?<name>[^:\n]+)(?::\s*(?<value>[^\n]*))?|(?<emptyLine>))\n/
                    ) ?? [];

                if (!substring) {
                    // didn't match
                    const newline = accumulator.indexOf('\n');
                    if (newline > -1) {
                        // we have a line of text that doesn't match the spec
                        throw new Error(`Invalid Server-Sent Event stream: ${accumulator.slice(0, newline + 1)}`);
                    }
                    // need more input to find the newline
                    break;
                }

                accumulator = accumulator.slice(substring.length);

                if (emptyLine === '') {
                    // emptyLine marks the end of one message and the start of another
                    // captured here as the empty string between subsequent newlines
                    // like a zero-width assertion of sorts, otherwise it's undefined
                    if (message) {
                        yield message;
                        message = undefined;
                    }
                } else if (name) {
                    // each message field has a name followed by optional colon and value
                    if (name === 'data' && value.match(/\[DONE\]/)) {
                        return;
                    }
                    if (name === 'event' && value === 'message_stop') {
                        return;
                    }
                    message ??= {};
                    message[name] = value;
                } else if (comment != null) {
                    // ignore comments
                }
            }
        }
    } catch (e) {
        const error = new Error(`Error parsing Server-Sent Event stream: ${e.message}`);
        error.cause = e;
        throw error;
    }
}
