import { LitElement, css, html, nothing, noChange } from 'lit';
import '@lit-labs/virtualizer';
import { unsafeHTML } from 'lit/directives/unsafe-html.js';
import chatOutputCss from './chatOutput.css.js';
import { normalizedBrightnessLogo, svgObjectUrl } from './svgLogos.js';
import { classMap } from 'lit/directives/class-map.js';
import { JsonStreamParser } from './jsonStream.js';
import { store, selection } from './store.js';
import { connect } from 'pwa-helpers/connect-mixin.js';
import { keyMerge } from './util.js';

// @ts-ignore
const converter = new showdown.Converter({
    disableForced4SpacesIndentedSublists: true,
    emoji: true,
    ghCodeBlocks: true,
    requireSpaceBeforeHeadingText: true,
    tables: true,
    openLinksInNewWindow: true,
    simpleLineBreaks: true,
    simplifiedAutoLink: true,
    smoothLivePreview: true,
});

const numberFormat = new Intl.NumberFormat();

class ChatEntry {
    role = 'assistant';
    reasoning = '';
    comment = '';
    commentParts = [];
    reasoningParts = [];
    hasCommentContent() {
        return this.comment || this.commentParts.length > 1 || this.commentParts[0];
    }
    hasReasoningContent() {
        return this.reasoning || this.reasoningParts.length > 1 || this.reasoningParts[0];
    }
    isLast = true;
}

class ChatOutput extends connect(store)(LitElement) {
    static styles = chatOutputCss;
    static properties = {
        conversation: { type: Array },
        playing: { type: Boolean, reflect: true },
        provider: { type: String },
        side: { type: String, attribute: true },
        settings: {
            type: Object,
            converter: attr => {
                return document.querySelector(attr);
            },
        },
        usage: { type: Object },
        intro: { type: Boolean },
    };

    constructor() {
        super();
        this.wait = Promise.resolve();
        this.paused = false;
        this.conversation = [new ChatEntry()];
        this.playing = false;
        this.provider = '';
        this.settings = null;
        this.scrollElem = null;
        this.intro = true;
        this.contentParser = null;
        this.side = '';
        this.usage = { input_tokens: 0, output_tokens: 0, total_tokens: 0 };
    }

    stateChanged(state) {
        const selectors = selection(state);
        this.provider = selectors.provider(this.side);
        if (this.bgStyle) {
            this.bgImageUrl().then(url => this.bgStyle.setProperty('--bg-image', url));
        }

        const tokenFields = {
            input_tokens: ['input_tokens', 'prompt_tokens'],
            output_tokens: ['output_tokens', 'completion_tokens'],
        };
        const usageByThread = selectors.currentUsage?.[this.side];
        if (!usageByThread) return;
        // @ts-ignore
        this.usage = keyMerge([usageByThread], tokenFields);
    }

    firstUpdated() {
        this.scrollElem = this.shadowRoot.querySelector('lit-virtualizer');
        setTimeout(() => (this.intro = false));

        const stylesheet = new CSSStyleSheet();
        stylesheet.replaceSync(css`
            :host {
                --bg-image: none;
            }
        `.cssText);
        this.shadowRoot.adoptedStyleSheets ??= [];
        this.shadowRoot.adoptedStyleSheets.push(stylesheet);
        this.bgStyle = /** @type CSSStyleRule */ (stylesheet.cssRules[0]).style;
    }

    reset() {
        this.conversation = [new ChatEntry()];
        this.usage = { input_tokens: 0, output_tokens: 0, total_tokens: 0 };
        this.requestUpdate();
        this.followScroll();
    }

    async streamMessage(message, type = 'comment_part') {
        this.followScroll();
        this.playing = true;
        if (type === 'comment_part') {
            if (!this.contentParser) {
                this.contentParser = new JsonStreamParser();
                const valuePartListener = e => {
                    if (e.path === 'comment') {
                        this.conversation.at(-1).commentParts.push(e.value);
                        this.requestUpdate('conversation');
                        this.followScroll();
                    }
                };
                this.contentParser.addEventListener('part', valuePartListener);
            }
            this.contentParser.push(message);
        } else {
            const chatEntry = this.conversation.at(-1);
            chatEntry.reasoningParts.push(message);
            this.requestUpdate('conversation');
            this.followScroll();
        }
    }

    followScroll() {
        this.updateComplete.then(_ => {
            this.scrollElem.scrollToIndex(this.conversation.length - (this.playing ? 1 : 2), 'end');
        });
    }

    addMessage(message) {
        const messageNum = this.conversation.length - 1;
        const { commentParts } = this.conversation[messageNum];
        if (commentParts.length && commentParts.join('') !== message) {
            console.warn(
                {
                    commentParts: commentParts.join(''),
                    comment: message,
                },
                `are not equal`,
            );
        }
        this.conversation[messageNum].comment = message;
        this.conversation[messageNum].isLast = false;
        this.conversation.push(new ChatEntry());
        this.requestUpdate();
        this.followScroll();
        this.playing = false;
        this.contentParser = null;
    }

    async bgImageUrl() {
        if (!this.provider) return 'none';
        const logo = await normalizedBrightnessLogo(this.provider);
        return `url(${svgObjectUrl(this.provider, logo)})`;
    }

    renderComment(chatEntry) {
        const comment = chatEntry.comment || chatEntry.commentParts.join('');
        if (comment) {
            return html`<div class="comment">${unsafeHTML(converter.makeHtml(comment))}</div>`;
        }
        return nothing;
    }

    renderReasoning(chatEntry) {
        const reasoning = chatEntry.reasoning || chatEntry.reasoningParts.join('');
        if (reasoning) {
            return html`<div class="reasoning">${unsafeHTML(converter.makeHtml(reasoning))}</div>`;
        }
        return nothing;
    }

    renderChatEntry(chatEntry, index) {
        const active = this.playing && chatEntry.isLast;
        return html`<div class="chatEntry ${classMap({ active })}">
            ${chatEntry.hasReasoningContent() ? this.renderReasoning(chatEntry) : nothing}
            ${chatEntry.hasCommentContent() ? this.renderComment(chatEntry) : nothing}
            ${active && !chatEntry.hasCommentContent() && !chatEntry.hasReasoningContent()
                ? html`<span style="width:100%" class="throbber"></span>`
                : nothing}
        </div>`;
    }

    renderMessages(messages) {
        return html`<lit-virtualizer
            class="chats"
            .items=${messages}
            .renderItem=${this.renderChatEntry.bind(this)}
        ></lit-virtualizer>`;
    }

    render() {
        if (this.paused) {
            this.wait.then(_ => this.requestUpdate());
            return noChange;
        }
        const end = this.playing ? this.conversation.length : this.conversation.length - 1;
        const messages = /** @type ChatEntry[] */ (this.conversation.slice(0, end));
        return html`<div class="scroll ${this.intro ? 'intro' : ''}">${this.renderMessages(messages)}</div>
            <div class="usage">
                tokens = { in: ${numberFormat.format(this.usage.input_tokens)}, out:
                ${numberFormat.format(this.usage.output_tokens)}, total:
                ${numberFormat.format(this.usage.input_tokens + this.usage.output_tokens)} }
            </div>`;
    }
}
customElements.define('chat-output', ChatOutput);
