import { LitElement, html, nothing } from 'lit';
import { ref, createRef } from 'lit/directives/ref.js';
import dialogStyles from './dialog.css.js';

export class ModalDialog extends LitElement {
    static styles = dialogStyles;

    static properties = {
        open: { type: Boolean },
        title: { type: String },
        content: { type: Object },
        actions: { type: Array },
    };

    constructor() {
        super();
        this.open = false;
        this.title = '';
        this.dialogRef = createRef();
    }

    async show({ title, content, actions }) {
        this.title = title;
        this.content = content;
        this.actions = actions;
        this.open = true;
        this.requestUpdate();
        return this.updateComplete.then(
            () =>
                new Promise(resolve => {
                    const dialog = /** @type HTMLDialogElement */ (this.dialogRef.value);
                    dialog.addEventListener('close', _ => resolve(dialog.returnValue), { once: true });
                    dialog.showModal();
                }),
        );
    }

    close(e) {
        e.preventDefault();
        e.stopPropagation();
        const dialog = /** @type HTMLDialogElement */ (this.dialogRef.value);
        dialog.close(e.target.value);
        this.open = false;
    }

    render() {
        if (!this.open) return null;

        return html`
            <dialog ${ref(this.dialogRef)}>
                <div class="dialog-header">
                    <span class="dialog-title">${this.title}</span>
                </div>
                <div class="dialog-content">${this.content}</div>
                <div class="dialog-actions" @click=${this.close}>${this.actions}</div>
            </dialog>
        `;
    }
}

export class NotificationDialog extends LitElement {
    static styles = dialogStyles;

    static properties = {
        _open: { type: Boolean, state: true }, // Private property
        title: { type: String },
        content: { type: Object },
    };

    constructor() {
        super();
        this._open = false;
        this.title = '';
        this.content = '';
        this.message = '';
        this.dialogRef = createRef();
    }

    get dialog() {
        return /** @type HTMLDialogElement */ (this.dialogRef.value);
    }

    firstUpdated() {
        this.addEventListener('click', (/** @type MouseEvent */ event) => {
            if (!this._open) return;
            const rect = this.dialog.getBoundingClientRect();
            const isInDialog =
                rect.top <= event.clientY &&
                event.clientY <= rect.bottom &&
                rect.left <= event.clientX &&
                event.clientX <= rect.right;

            if (!isInDialog) {
                event.stopPropagation();
                event.preventDefault();
                this.close();
            }
        });
    }

    render() {
        // if (!this._open) return nothing;

        return html`
            <dialog ${ref(this.dialogRef)} ?open="${this._open}" class="notification">
                <div class="dialog-header">
                    <span class="dialog-title">${this.title}</span>
                    <button class="close-button" @click=${this.close}>×</button>
                </div>
                <div class="dialog-content">${this.content}</div>
            </dialog>
        `;
    }

    async show({ title, content, autoDismiss = 0 }) {
        return new Promise(resolve => {
            this.title = title;
            this.content = content;
            this._open = true;
            if (autoDismiss > 0) {
                setTimeout(_ => this.close(), autoDismiss);
            }
            this.addEventListener('notification-close', () => resolve(), { once: true });
        });
    }

    close() {
        this._open = false;
        this.dialog.close();
        this.dispatchEvent(new CustomEvent('notification-close'));
    }
}

customElements.define('modal-dialog', ModalDialog);
customElements.define('notification-dialog', NotificationDialog);
