export default class BaseApiHandler {
    constructor(config) {
        this.config = config;
    }

    formatUrl(baseUrl) {
        throw new Error('formatUrl must be implemented');
    }
    formatMessages(messages) {
        throw new Error('formatMessages must be implemented');
    }

    createRequestPayload(messages) {
        throw new Error('createRequestPayload must be implemented');
    }

    getHeaders() {
        throw new Error('getHeaders must be implemented');
    }

    handleResponseJson(responseJson, expectingJsonContent) {
        throw new Error('handleResponseJson must be imolemented');
    }

    parseStreamChunk(data) {
        throw new Error('parseStreamChunk must be implemented');
    }
}
