/**
 * @typedef {Object} Range
 * @property {number} min  0 to 1
 * @property {number} max  0 to 1
 */
const _filters = {
    desaturate: ({ values = 0.75 }) => svg`<feColorMatrix type="saturate" values="${values}" />`,

    normalizeBrightness: ({ rangeIn, rangeOut }) => {
        const slope = (rangeOut.max - rangeOut.min) / (rangeIn.max - rangeIn.min);
        const intercept = rangeOut.min - rangeIn.min * slope;
        return svg`<feComponentTransfer>
            <feFuncR type="linear" slope="${slope}" intercept="${intercept}"/>
            <feFuncG type="linear" slope="${slope}" intercept="${intercept}"/>
            <feFuncB type="linear" slope="${slope}" intercept="${intercept}"/>
        </feComponentTransfer>`;
    },
    opacity: ({ value = 1 }) =>
        svg`<feComponentTransfer><feFuncA type="linear" slope="${value}"/></feComponentTransfer>`,
    blur: ({ value = 1 }) => svg`<feGaussianBlur stdDeviation="${value}" />`,
};

export const logo = name => zipSvg(_logos[name]);
export const filter = primitives =>
    Object.entries(primitives)
        .map(([name, options]) => zipSvg(_filters[name](options)))
        .join('');

const urlCache = {};

export const svgObjectUrl = (name, svg) =>
    name in urlCache
        ? urlCache[name]
        : (urlCache[name] = URL.createObjectURL(new Blob([svg], { type: 'image/svg+xml;utf8' })));

export const logoAsObjectUrl = name => svgObjectUrl(name, logo(name));

export const svgDataUrl = svgContents => `data:image/svg+xml;utf8,${encodeURIComponent(svgContents)}`;

export const desaturatedLogoAsObjectUrl = name => {
    const modifiedName = `${name}:desaturated`;
    if (urlCache[modifiedName]) return urlCache[modifiedName];
    const svg = logo[name].strings[0].replace(/<![^>]+>/g, '');
    const filterUrl = svgDataUrl(filter({ desaturate: { value: 0 } })) + '#desaturate';
    const svgWithFilter = svg.replace(/<svg/, `<svg filter="url(${filterUrl})"`);

    const svgUrl = URL.createObjectURL(new Blob([svgWithFilter], { type: 'image/svg+xml;utf8' }));
    urlCache[modifiedName] = svgUrl;
    return svgUrl;
};

export const wrapLogoWithFilters = (svgContent, filters) =>
    zipSvg(svg`<svg version="1.1" xmlns="http://www.w3.org/2000/svg">
        <defs><filter id="filter">${Object.values(filters).join('')}</filter></defs>
        <g filter="url(#filter)">${svgContent}</g>
    </svg>`);

function analyzeBrightness(ctx, width, height) {
    // Get pixel data
    const imageData = ctx.getImageData(0, 0, width, height);
    const { data } = imageData;

    let minBrightness = 255; // Initialize to max brightness
    let maxBrightness = 0; // Initialize to min brightness

    // Loop through pixel data
    for (let i = 0; i < data.length; i += 4) {
        // Extract RGB values
        const r = data[i];
        const g = data[i + 1];
        const b = data[i + 2];

        // Convert to brightness (e.g., luminance formula)
        const brightness = 0.299 * r + 0.587 * g + 0.114 * b;

        // Update min/max brightness
        minBrightness = Math.min(minBrightness, brightness);
        maxBrightness = Math.max(maxBrightness, brightness);
    }
    return { min: minBrightness / 255, max: maxBrightness / 255 };
}

export const normalizedBrightnessLogo = async name => {
    const img = new Image();
    img.src = svgDataUrl(wrapLogoWithFilters(logo(name), { desaturate: { values: 0 } }));
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    return new Promise((resolve, reject) => {
        img.onload = () => {
            // Set canvas size to match the SVG dimensions
            if (!img.width || !img.height) {
                img.width = 300;
                img.height = 150;
            }
            canvas.width = img.width;
            canvas.height = img.height;

            // Draw the SVG image onto the canvas
            ctx.drawImage(img, 0, 0);

            // Analyze pixel data
            const rangeIn = analyzeBrightness(ctx, img.width, img.height);

            // Create a brightness normalization filter
            let result = wrapLogoWithFilters(
                logo(name),
                filter({
                    normalizeBrightness: {
                        rangeIn,
                        rangeOut: { min: 0.25, max: 0.25 },
                    },
                    desaturate: { values: 0 },
                    // blur: { value: 1.5 },
                    opacity: { value: name === 'deepseek' ? 0.2 : 0.1 },
                }),
            );
            resolve(result);
        };
    });
};

export const zip = (a, b) => a.flatMap((k, i) => [k, b[i]]);
export const zipSvg = svg => zip(svg.strings, svg.values).join('');
