// @ts-nocheck
import { init } from '@rematch/core';
import selectPlugin from '@rematch/select';
import persistPlugin from '@rematch/persist';
import storage from 'redux-persist-indexeddb-storage';
import createCompressor from 'redux-persist-transform-compress';
// import storage from 'redux-persist/lib/storage';
import { sumOf, assert, objectFrom, entriesOf } from './util.js';
import chatProviders from './chatProviders.js';

const colors = { r: 'red', R: 'red', b: 'black', B: 'black' };
const ranks = { r: 'pawn', R: 'king', b: 'pawn', B: 'king' };
const columns = [...'ABCDEFGH'];
const positions = {};
for (let n = 0; n < 12; n++) {
    const rowCol = n / 4;
    const row = Math.floor(rowCol);
    const col = (rowCol % 1) * 8 + ((1 + row) % 2);
    positions[`${columns[col]}${row + 1}`] = `b${n}`;
    positions[`${columns[7 - col]}${8 - row}`] = `r${n}`;
}

const models = {
    currentMatch: {
        name: 'currentMatch',
        state: {
            id: '',
        },
        reducers: {
            start(state, { red, black }) {
                const id = crypto.randomUUID();
                const started = Date.now();
                return { ...state, id, started, red, black, turns: [] };
            },
            turn(state, { turn }) {
                const usage = sumOf([state.usage ?? {}, { [turn.color]: turn.response?.usage ?? {} }]);

                return { ...state, usage, turns: [...state.turns, turn] };
            },
            'board/capture': (state, payload) => {
                const turn = state.turns.at(-1);
                const captures = turn.captures ?? [];
                return {
                    ...state,
                    turns: [...state.turns.slice(0, -1), { ...turn, captures: [...captures, payload.position] }],
                };
            },
            end(state, { result }) {
                return { ...state, result, ended: Date.now() };
            },
            reset(state) {
                return {};
            },
        },
        effects: dispatch => ({
            start(payload, state) {},
            end(payload, state) {
                dispatch.matches.add({ match: { ...state.currentMatch } });
                dispatch.currentMatch.reset();
            },
            turn(payload, state) {},
        }),
        selectors: (slice, createSelector, hasProps) => ({
            usage() {
                return createSelector(slice, current => current?.usage ?? {});
            },
        }),
    },
    matches: {
        name: 'matches',
        state: [],
        reducers: {
            add: (state, { match }) => {
                return [...state, match];
            },
        },
        selectors: (slice, createSelector, hasProps) => ({
            matches() {
                return createSelector(slice, state => state);
            },
            usageByProvider() {
                return createSelector(slice, state => provider => {
                    const relevantTurns = match =>
                        match.turns?.filter(t => match[t.color]?.provider === provider && t.response?.usage) ?? [];
                    return sumOf(state.flatMap(relevantTurns).map(t => t.response?.usage ?? {}));
                });
            },
            matchesByProvider() {
                return createSelector(
                    slice,
                    state => provider =>
                        state.filter(match => match.red.provider === provider || match.black.provider === provider),
                );
            },
            matchesWonByProvider() {
                return createSelector(
                    this.matchesByProvider,
                    matchesByProvider => provider =>
                        matchesByProvider(provider).filter(match => match[match.result?.winner]?.provider === provider),
                );
            },
            matchesWonAndLostByProvider() {
                return createSelector(slice, state =>
                    state.reduce((acc, match) => {
                        if (!match.result || match.result.winner === 'Error') {
                            return acc;
                        }
                        const winner = match[match.result.winner]?.provider ?? 'other';
                        const loser = match[match.result.winner === 'red' ? 'black' : 'red']?.provider ?? 'other';
                        acc[winner] ??= { wins: [], losses: [] };
                        acc[loser] ??= { wins: [], losses: [] };
                        acc[winner].wins.push(match);
                        acc[loser].losses.push(match);
                        return acc;
                    }, {}),
                );
            },
        }),
    },
    board: {
        name: 'board',
        state: {
            positions: { ...positions },
            captures: [
                /* { aposition: 'A1', piece: 'b0' } */
            ],
        },
        reducers: {
            move(state, { from, to }) {
                const positions = { ...state.positions, [to]: state.positions[from] };
                delete positions[from];
                return { ...state, positions };
            },
            capture(state, { position }) {
                const positions = { ...state.positions };
                const piece = positions[position];
                delete positions[position];

                return {
                    ...state,
                    positions,
                    captures: [...state.captures, { color: colors[piece[0]], rank: ranks[piece[0]], position, piece }],
                };
            },
            promote(state, { position }) {
                const { positions } = state;
                return { ...state, positions: { ...positions, [position]: positions[position].toUpperCase() } };
            },
            reset(state) {
                return { ...state, positions: { ...positions }, turns: [], captures: [] };
            },
        },
        selectors: (slice, createSelector, hasProps) => ({
            pieces() {
                return createSelector(slice, board => board.positions);
            },
            piece() {
                return createSelector(this.pieces, pieces => position => pieces[position]);
            },
            occupied() {
                return createSelector(this.piece, piece => position => !piece(position));
            },
            pieceColor() {
                return createSelector(this.piece, piece => position => colors[piece(position)[0]]);
            },
            rank() {
                return createSelector(this.piece, piece => position => ranks[piece(position)[0]]);
            },
            captures() {
                return slice(board =>
                    board.captures.map(cap => ({ color: colors[cap.piece[0]], rank: ranks[cap.piece[0]], ...cap })),
                );
            },
            redCaptures() {
                return createSelector(this.captures, captures => captures.filter(({ color }) => color === 'red'));
            },
            blackCaptures() {
                return createSelector(this.captures, captures => captures.filter(({ color }) => color === 'black'));
            },
        }),
    },
    apiKeys: {
        name: 'apiKeys',
        state: {
            apiKeys: {
                openai: '',
                anthropic: '',
                google: '',
                mistral: '',
                deepseek: '',
                openrouter: '',
            },
        },
        reducers: {
            /**
             * Set the API key for a provider
             * @param {Object} state
             * @param {Object} payload\\
             * @returns {object} updated state
             */
            set(state, { provider, apiKey, ...providerApiKeys }) {
                if (provider) {
                    return { ...state, apiKeys: { ...state.apiKeys, [provider]: apiKey } };
                }
                providerApiKeys = objectFrom(entriesOf(providerApiKeys).filter(([k]) => k in chatProviders));
                return { ...state, apiKeys: { ...state.apiKeys, ...providerApiKeys } };
            },

            'persist/REHYDRATE': (state, incoming, key) => {
                if (incoming?.settings?.ui?.saveApiKeys) {
                    const providerApiKeys = objectFrom(
                        entriesOf(incoming.settings.recentApiKeys).filter(([k, v]) => k in chatProviders),
                    );
                    return { ...state, apiKeys: { ...state.apiKeys, ...providerApiKeys } };
                }
                return state;
            },
        },
        effects: dispatch => ({
            async sendKeys(payload, state) {
                // saving of apikeys across sessions was just authorized by the user so they
                // need a full copy as a starting point. normally they're just maintained
                // here this model which never gets persisted until they can. we both listen
                // for action that change their state.
                dispatch.apiKeys.set({ ...state.apiKeys.apiKeys });
            },
        }),
        selectors: (slice, createSelector, hasProps) => ({
            apiKeys() {
                return createSelector(slice, state => state.apiKeys);
            },
            apiKey() {
                return createSelector(slice, state => provider => state.apiKeys[provider]);
            },
            provider() {
                return createSelector(slice, state => color => state.provider[color]);
            },
            apiKeyByColor() {
                return createSelector(
                    this.provider,
                    this.apiKey,
                    (provider, apiKey) => color => apiKey(provider(color)),
                );
            },
        }),
    },
    settings: {
        name: 'settings',
        state: {
            recentModels: {
                openai: chatProviders['openai'].defaultModel,
                anthropic: chatProviders['anthropic'].defaultModel,
                google: chatProviders['google'].defaultModel,
                mistral: chatProviders['mistral'].defaultModel,
                deepseek: chatProviders['deepseek'].defaultModel,
                openrouter: chatProviders['openrouter'].defaultModel,
                lmstudio: chatProviders['lmstudio'].defaultModel,
                ollama: chatProviders['ollama'].defaultModel,
            },
            recentBaseUrls: {
                ollama: chatProviders['ollama'].baseUrl,
                lmstudio: chatProviders['lmstudio'].baseUrl,
            },
            recentApiKeys: {
                // only saved when permitted by the user
                openai: '',
                anthropic: '',
                google: '',
                mistral: '',
                deepseek: '',
                openrouter: '',
            },
            players: {
                red: {
                    provider: 'openai',
                    model: chatProviders['openai'].defaultModel,
                    stream: true,
                    temperature: 1.0,
                    baseUrl: chatProviders['openai'].baseUrl,
                    maxTokens: 2048,
                    contextLength: 4096,
                },
                black: {
                    provider: 'anthropic',
                    model: chatProviders['anthropic'].defaultModel,
                    stream: true,
                    temperature: 1.0,
                    baseUrl: chatProviders['anthropic'].baseUrl,
                    maxTokens: 2048,
                    contextLength: 4096,
                },
            },
            ui: {
                saveApiKeys: false,
            },
        },
        reducers: {
            /**
             * Set the provider for a player
             * @param {Object} stqte color - 'red' or 'black'
             * @param {Object} payload color - 'red' or 'black'
             * @returns {object} updated state
             */
            setPlayerProvider(state, { color, provider }) {
                const player = {
                    ...state.players[color],
                    provider,
                    model: state.recentModels[provider] || chatProviders[provider].defaultModel,
                    baseUrl: state.recentBaseUrls[provider] || chatProviders[provider].baseUrl,
                    stream: true,
                    temperature: 1.0,
                    maxTokens: 2048,
                    contextLength: 4096,
                };
                return { ...state, players: { ...state.players, [color]: player } };
            },
            /**
             * Set the model for a player
             * @param {Object} state
             * @param {Object} payload
             * @returns {object} updated state
             */
            setPlayerModel(state, { color, model }) {
                const { provider } = state.players[color];
                const recentModels = { ...state.recentModels, [provider]: model };
                const player = { ...state.players[color], model };
                return { ...state, recentModels, players: { ...state.players, [color]: player } };
            },
            setPlayerBaseUrl(state, { color, baseUrl }) {
                const { provider } = state.players[color];
                const recentBaseUrls = { ...state.recentBaseUrls, [provider]: baseUrl };
                const player = { ...state.players[color], baseUrl };
                return { ...state, recentBaseUrls, players: { ...state.players, [color]: player } };
            },
            setSaveApiKeys(state, { save }) {
                if (!save) {
                    return { ...state, recentApiKeys: {}, ui: { ...state.ui, saveApiKeys: save } };
                }
                return { ...state, ui: { ...state.ui, saveApiKeys: save } };
            },
            setUi(state, { key, value }) {
                return { ...state, ui: { ...state.ui, [key]: value } };
            },
            'apiKeys/set': (state, { provider, apiKey, ...providerKeys }) => {
                if (state.ui.saveApiKeys) {
                    if (provider) {
                        return { ...state, recentApiKeys: { ...state.recentApiKeys, [provider]: apiKey } };
                    }
                    providerKeys = objectFrom(entriesOf(providerKeys).filter(([k]) => k in chatProviders));
                    return { ...state, recentApiKeys: { ...state.recentApiKeys, ...providerKeys } };
                }
                return state;
            },
        },
        effects: dispatch => ({
            setSaveApiKeys({ save }, state) {
                if (save) {
                    dispatch.apiKeys.sendKeys();
                }
            },
        }),
        selectors: (slice, createSelector, hasProps) => ({
            player() {
                return createSelector(slice, settings => color => settings.players[color]);
            },
            provider() {
                return createSelector(this.player, player => color => player(color).provider);
            },
            currentModel() {
                return createSelector(this.player, player => color => player(color).model);
            },
            currentApiKey() {
                return createSelector(this.player, player => color => player(color).apiKey);
            },
            currentBaseUrl() {
                return createSelector(this.player, player => color => player(color).baseUrl);
            },
            recentModel() {
                return createSelector(
                    slice,
                    this.provider,
                    (settings, provider) => color => settings.recentModels[provider(color)],
                );
            },
            recentApiKey(models) {
                return createSelector(
                    slice,
                    this.provider,
                    (settings, provider) => color =>
                        settings.recentApiKeys[provider(color)] || models.apiKeys.apiKey[provider(color)],
                );
            },
            recentBaseUrl() {
                return createSelector(this.player, player => color => player(color).baseUrl);
            },
            model() {
                return createSelector(
                    this.currentModel,
                    this.recentModel,
                    (current, recent) => color => current(color) || recent(color),
                );
            },
            apiKey() {
                return createSelector(
                    this.currentApiKey,
                    this.recentApiKey,
                    (current, recent) => color => current(color) || recent(color),
                );
            },
            baseUrl() {
                return createSelector(
                    this.currentBaseUrl,
                    this.recentBaseUrl,
                    (current, recent) => color => current(color) || recent(color),
                );
            },
            ui() {
                return createSelector(slice, settings => settings.ui);
            },
            saveApiKeys() {
                return createSelector(this.ui, ui => ui.saveApiKeys);
            },
            canPlay({ apiKeys }) {
                return createSelector(this.provider, this.baseUrl, apiKeys.apiKey, (provider, baseUrl, apiKey) => {
                    const redProvider = chatProviders[provider('red')];
                    const redNeedsConfig =
                        !redProvider || redProvider.apiKeyName ? !apiKey(provider('red')) : !baseUrl('red');
                    const blackProvider = chatProviders[provider('black')];
                    const blackNeedsConfig =
                        !blackProvider || blackProvider.apiKeyName ? !apiKey(provider('black')) : !baseUrl('black');
                    return !redNeedsConfig && !blackNeedsConfig;
                });
            },
        }),
    },
    threads: {
        name: 'threads',
        state: {}, // {[request.to]: [{request: {}, responseParts: [{}, ...], response: {}}, ...]. ...}
        reducers: {
            addRequest(state, request) {
                const { subject, ...reqNoSubject } = request;
                const thread = state[subject] ?? [];
                const id = thread.length;
                return { ...state, [subject]: [{ id, request: reqNoSubject }, ...thread] };
            },
            addResponsePart(state, responsePart) {
                const { subject, part } = responsePart;
                const [{ id, request, responseParts = [], response }, ...thread] = state[subject];
                assert(request, 'Missing request in message');
                assert(!response, 'Duplicate response in message');
                return {
                    ...state,
                    [subject]: [{ id, request, responseParts: [...responseParts, part] }, ...thread],
                };
            },
            addResponse(state, response) {
                const { subject, ...respNoSubject } = response;
                const [{ id, request, responseParts, response: dupe }, ...thread] = state[subject];
                assert(request, 'Missing request in message');
                assert(!dupe, 'Duplicate response in message');
                return {
                    ...state,
                    [subject]: [{ id, request, responseParts, response: respNoSubject }, ...thread],
                };
            },
            clear(state) {
                return [];
            },
        },
        selectors: (slice, createSelector, hasProps) => ({
            thread() {
                return createSelector(slice, threads => subject => threads[subject]);
            },
            exchangeById() {
                return createSelector(this.thread, thread => subject => id => thread(subject)?.at(-id - 1));
            },
            exchangeById2() {
                return createSelector(
                    this.thread,
                    thread =>
                        ({ subject, id }) =>
                            thread(subject).at(-id - 1),
                );
            },
            exchangeByProps: hasProps((_, { subject, id }) => {
                return createSelector(slice, threads => {
                    return threads[subject]?.at(-id - 1);
                });
            }),
            requestById() {
                return createSelector(
                    this.exchangeById,
                    (exchange, subject, id) => exchange(subject).at(-id - 1).request,
                );
            },
            responseById() {
                return createSelector(
                    this.exchangeById,
                    exchange => subject => id => exchange(subject).at(-id - 1).response,
                );
            },
            partsByResponseId() {
                return createSelector(
                    this.exchangeById,
                    exchange => subject => id => exchange(subject).at(-id - 1).responseParts,
                );
            },
            exchangesAfterId() {
                return createSelector(
                    this.thread,
                    thread => subject => id =>
                        thread(subject)
                            .slice(0, -id - 1)
                            .reverse(),
                );
            },
            requestsAfterId() {
                return createSelector(
                    this.exchangesAfterId,
                    exchangesAfterId => subject => id =>
                        exchangesAfterId(subject)(id).map(({ id, request }) => ({ id, request })),
                );
            },
            responsesAfterId() {
                return createSelector(
                    this.exchangesAfterId,
                    exchangesAfterId => subject => id =>
                        exchangesAfterId(subject)(id).map(({ id, response }) => ({
                            id,
                            response,
                        })),
                );
            },
            partsByResponseIdAfterId() {
                return createSelector(
                    this.partsByResponseId,
                    partsByResponseId => subject => responseId => id =>
                        partsByResponseId(subject)(responseId)
                            .slice(id + 1)
                            .map(part => part.content),
                );
            },
            usageByThread() {
                return createSelector(
                    this.thread,
                    thread => subject => sumOf(thread(subject)?.map(({ response }) => response?.message?.usage ?? {})),
                );
            },
        }),
    },
};

const persistConfig = {
    key: 'root',
    storage: storage.default('chatsworth'),
    transforms: [createCompressor.default()],
    blacklist: ['board', 'threads', 'apiKeys', 'currentMatch'],
};

// @ts-ignore
export const store = init({ models, plugins: [selectPlugin(), persistPlugin(persistConfig)] });
export const selection = store.select(models => ({
    pieces: models.board.pieces,
    piece: models.board.piece,
    rank: models.board.rank,
    color: models.board.pieceColor,
    redCaptures: models.board.redCaptures,
    blackCaptures: models.board.blackCaptures,
    currentUsage: models.currentMatch.usage,
    usageByProvider: models.matches.usageByProvider,
    matches: models.matches.matches,
    matchesByProvider: models.matches.matchesByProvider,
    matchesWonByProvider: models.matches.matchesWonByProvider,
    matchesWonAndLostByProvider: models.matches.matchesWonAndLostByProvider,
    apiKeys: models.apiKeys.apiKeys,
    apiKeyByProvider: models.apiKeys.apiKey,
    player: models.settings.player,
    provider: models.settings.provider,
    currentModel: models.settings.currentModel,
    currentApiKey: models.settings.currentApiKey,
    currentBaseUrl: models.settings.currentBaseUrl,
    recentModel: models.settings.recentModel,
    recentApiKey: models.settings.recentApiKey,
    recentBaseUrl: models.settings.recentBaseUrl,
    model: models.settings.model,
    apiKey: models.settings.apiKey,
    baseUrl: models.settings.baseUrl,
    ui: models.settings.ui,
    saveApiKeys: models.settings.saveApiKeys,
    canPlay: models.settings.canPlay,
}));
