import BaseApiHandler from './baseApiHandler.js';

export default class AnthropicHandler extends BaseApiHandler {
    formatUrl(baseUrl) {
        return `${baseUrl}/messages`;
    }

    formatMessages(messages) {
        return messages.map(msg => ({
            role: msg.role === 'assistant' ? 'assistant' : 'user',
            content: msg.content,
        }))
    }

    createRequestPayload(messages) {
        return {
            model: this.config.model,
            messages: this.formatMessages(messages),
            max_tokens: this.config.maxTokens ?? 8192,
            temperature: this.config.temperature,
            stream: this.config.stream,
            tool_choice: {
                type: 'tool',
                name: 'CheckersMove'
            },
            tools: [{
                name: 'CheckersMove',
                description: 'Take your turn at checkers',
                input_schema: this.config.responseFormat.json_schema.schema
            }]
        };
    }

    getHeaders() {
        const headers = {
            'Content-Type': 'application/json',
            'x-api-key': this.config.apiKey,
            'anthropic-version': this.config.anthropicVersion,
        };

        if (globalThis.navigator) {
            headers['anthropic-dangerous-direct-browser-access'] = 'true';
        }
        return headers;
    }

    handleResponseJson(responseJson) {
        if (responseJson.content.length !== 1) {
            console.warn(`response includes ${responseJson.content.length} content blocks.`);
        }
        const contentBlock = responseJson.content[0];
        const content = contentBlock.type === 'text' ? contentBlock.text : contentBlock.type === 'tool_use' ? JSON.stringify(contentBlock.input) : undefined
        const message = { role: responseJson.role, content };
        const usage = responseJson.usage;

        return { content, message, usage };
    }

    accum = [];
    parseStreamChunk(event, message) {
        const parsed = JSON.parse(event.data);
        let partialContent = '';
        if (parsed.type === 'content_block_delta') {
            if (parsed.delta?.type === 'text_delta') {
                partialContent = parsed.delta.text;
            } else if (parsed.delta?.type === 'input_json_delta') {
                partialContent = parsed.delta.partial_json;
            }
        }

        event.data = parsed;
        switch (event.event) {
            case 'message_start':
                message = { ...event.data.message };
                break;

            case 'content_block_start':
                message.content[event.data.index] = event.data.content_block;
                break;

            case 'content_block_delta':
                const partial = event.data.delta.type === 'input_json_delta' ? 'partial_json' : 'text';
                this.accum.push(event.data.delta[partial]);
                break;

            case 'content_block_stop':
                const type = message.content[event.data.index].type;
                const content = type === 'tool_use' ? JSON.parse(this.accum.join('')) : this.accum.join('');
                const field = type === 'tool_use' ? 'input' : 'text';
                message.content[event.data.index][field] = content;
                this.accum.length = 0;
                break;

            case 'message_delta':
                message = { ...message, ...event.data.delta };
                break;

            case 'message_stop':
            case 'ping':
            default:
                break;
        }
        return { partialContent, message, parsed };
    }
}
