import { filterObject, mapObject } from '../util.js';
import BaseApiHandler from './baseApiHandler.js';

export default class OpenAIHandler extends BaseApiHandler {
    formatUrl(baseUrl) {
        return `${baseUrl}/chat/completions`;
    }

    formatMessages(messages) {
        return messages.map(({ role, content }) => ({ role, content }));
    }

    createRequestPayload(messages) {
        const payload = filterObject(
            {
                model: this.config.model,
                messages: this.formatMessages(messages),
                max_tokens: this.config.maxTokens,
                temperature: this.config.temperature,
                response_format: this.config.responseFormat,
                stream: this.config.stream,
                stream_options: this.config.stream ? { include_usage: true } : undefined,
            },
            value => value !== undefined,
        );

        if (this.config.provider === 'openai' && this.config.model === 'o1' && this.config.stream) {
            payload.stream = false;
            delete payload.stream_options;
        }

        if (this.config.provider === 'openai' && this.config.model.startsWith('o')) {
            payload.reasoning_effort = this.config.reasoningEffort ?? 'low';
        } else {
            delete payload.reasoning_effort;
        }

        if (this.config.provider === 'mistral') {
            delete payload.stream_options;
        }

        if (
            ['deepseek', 'openrouter'].includes(this.config.provider) &&
            payload.response_format &&
            payload.response_format.type === 'json_schema'
        ) {
            if (
                this.config.provider !== 'deepseek' ||
                (this.config.provider === 'deepseek' && this.config.model !== 'deepseek-reasoner')
            ) {
                const schema = payload.response_format.json_schema;
                delete payload.response_format;
                payload.tools = [
                    {
                        type: 'function',
                        function: {
                            name: schema.name,
                            description: schema.description,
                            parameters: schema.schema,
                        },
                    },
                ];
                payload.tool_choice = {
                    type: 'function',
                    function: {
                        name: 'CheckersMove',
                    },
                };
            }
            if (this.config.provider === 'deepseek' && this.config.model === 'deepseek-reasoner') {
                delete payload.temperature;
                delete payload.response_format;

                payload.messages[0].content +=
                    'Please respond in this format: {"comment": "your comment", "move": "your move"}';
            }
        }
        return payload;
    }

    getHeaders() {
        return {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${this.config.apiKey}`,
        };
    }

    handleResponseJson(responseJson) {
        if (responseJson.choices.length !== 1) {
            console.warn(`response includes ${responseJson.choices.length} choices.`);
        }
        const finishReason = responseJson.choices[0]?.finish_reason;
        const message = responseJson.choices[0]?.message;
        const usage = responseJson.usage;
        const content = finishReason === 'tool_calls' ? message.tool_calls[0].function.arguments : message?.content;
        return { content, message, usage };
    }

    parseStreamChunk({ data }, message) {
        const parsed = JSON.parse(data);
        let partialContent = '';
        let partialReasoningContent = '';

        if (!message.id) {
            message = {
                ...parsed,
                id: parsed.id ?? Date.now(),
                choices: [{ ...parsed.choices[0], message: parsed.choices[0].delta }],
            };
            partialContent = parsed.choices[0].delta.content ?? '';
            delete message.choices[0].delta;
        } else {
            if ('content' in (parsed.choices[0]?.delta ?? {}) && parsed.choices[0]?.delta?.content) {
                message.choices[0].message.content += partialContent = parsed.choices[0].delta.content ?? '';
            } else if ('tool_calls' in (parsed.choices[0]?.delta ?? {})) {
                const toolCall = parsed.choices[0].delta.tool_calls[0];
                message.choices[0].message.tool_calls ??= [];
                const accumulatedToolCalls = message.choices[0].message.tool_calls;
                const index = toolCall.index;
                accumulatedToolCalls[index] ??= { function: { arguments: '' } };
                accumulatedToolCalls[index].function.arguments += toolCall.function.arguments ?? '';
                if (index === 0) {
                    partialContent = toolCall.function.arguments ?? '';
                }
            } else if (
                'reasoning_content' in (parsed.choices[0]?.delta ?? {}) &&
                parsed.choices[0]?.delta?.reasoning_content
            ) {
                message.choices[0].message.reasoning_content ??= '';
                message.choices[0].message.reasoning_content += partialReasoningContent =
                    parsed.choices[0].delta.reasoning_content;
            }
            if (parsed.choices.length) {
                message.choices[0].finish_reason = parsed.choices[0].finish_reason;
            }
            if (parsed.usage) {
                message.usage = parsed.usage;
            }
        }
        return { partialReasoningContent, partialContent, parsed, message };
    }
}
