import { store, selection } from './store.js';
import { connect } from 'pwa-helpers/connect-mixin.js';
import { LitElement, html, noChange } from 'lit';
import { repeat } from 'lit/directives/repeat.js';
import { play } from './checkersController.js';
import { CheckersPlayer } from './checkersPlayer.js';
import checkerboardStyles from './checkerboard.css.js';
import { icon } from './icons.js';
import './chatsworth.js';
import { objectFrom, valuesOf } from './util.js';
import pieceStyles from './piece.css.js';

const columns = [...'ABCDEFGH'];
const rows = [1, 2, 3, 4, 5, 6, 7, 8];
const colZero = 'A'.charCodeAt(0);
const colors = { r: 'red', b: 'black', R: 'red', B: 'black' };
const ranks = { r: 'pawn', b: 'pawn', R: 'king', B: 'king' };

const allSquares = columns.flatMap(col => rows.map(row => `${col}${row}`));
const activeSquares = new Set(
    allSquares.filter((pos, i) => {
        const row = Math.floor(i / 8);
        const col = i % 8;
        return (row + col) % 2 !== 0;
    }),
);

['chat-settings', 'chat-output'].forEach(tag => {
    // @ts-ignore
    customElements.whenDefined(tag).then(_ => document.querySelectorAll(tag).forEach(el => (el.store = store)));
});

const querySelectedProperty = {
    type: Object,
    converter: attr => document.querySelector(attr),
};

/**
 * @property {Object} positions
 * @property {String} redPlayer
 * @property {String} blackPlayer
 */
class Checkerboard extends connect(store)(LitElement) {
    static styles = [checkerboardStyles, pieceStyles];

    static properties = {
        positions: { type: Object },
        redSettings: querySelectedProperty,
        blackSettings: querySelectedProperty,
        redThoughts: querySelectedProperty,
        blackThoughts: querySelectedProperty,
        canPlay: { type: Boolean },
        playing: { type: Boolean },
        paused: { type: Boolean },
        intro: { type: Boolean, reflect: true },
        perspective: { type: Boolean, reflect: true },
    };

    constructor() {
        super();
        this.redSettings = null;
        this.blackSettings = null;
        this.redThoughts = null;
        this.blackThoughts = null;
        this.wait = Promise.resolve();
        this.turn = 1;
        this.side = 'black';
        this.playing = false;
        this.intro = true;
        this.perspective = false;
        this.selection = selection;
        this.store = store;
    }

    stateChanged(state) {
        const selectors = selection(state);
        this.selectors = selectors;
        this.positions = selectors.pieces;
        this.positionPieceId = selectors.piece;
        this.positionColor = selectors.color;
        this.positionRank = selectors.rank;
        this.redCaptures = selectors.redCaptures;
        this.blackCaptures = selectors.blackCaptures;
        this.canPlay = selectors.canPlay;
        this.redSettings = selectors.player('red');
        this.blackSettings = selectors.player('black');
        this.apiKeyByProvider = selectors.apiKeyByProvider;
    }

    firstUpdated() {
        this.notificationDialog = customElements
            .whenDefined('notification-dialog')
            .then(_ => document.querySelector('notification-dialog'));

        setTimeout(_ => {
            this.intro = false;
            this.perspective = true;
        });

        document.addEventListener('ChatSettings:apiKey', _ => {
            this.requestUpdate();
        });
    }

    validateStep = (from, to, actionChar, _positions = { ...this.positions }) => {
        const fromRow = from[1];
        const fromId = _positions[from];
        const toRow = to[1];
        const fromCol = from.charCodeAt(0) - colZero;
        const toCol = to.charCodeAt(0) - colZero;
        if (!fromId) {
            throw new Error(`Invalid move from ${from} to ${to}: no piece at ${from}`);
        }
        const color = colors[fromId[0]];
        const rank = ranks[fromId[0]];
        const dx = toCol - fromCol;
        const dy = toRow - fromRow;

        if (rank === 'pawn' && ((color === 'red' && dy > 0) || (color === 'black' && dy < 0))) {
            throw new Error(`Invalid move from ${from} to ${to}: ${color} pawn can only move forward`);
        }
        const action = actionChar === 'x' ? 'jump' : 'move';
        if (Math.abs(dx) !== Math.abs(dy) || Math.abs(dx) > 2 || (action === 'move' && Math.abs(dx) !== 1)) {
            throw new Error(`Invalid ${action} from ${from} to ${to}: not a diagonal move <= 2 squares`);
        }
        if (_positions[to]) {
            throw new Error(`Invalid ${action} from ${from} to ${to}: destination occupied`);
        }
        let captured;
        if (action === 'jump') {
            const capturePos = `${columns[fromCol + dx / 2]}${+fromRow + dy / 2}`;
            const captureId = _positions[capturePos];
            const oppColor = color === 'red' ? 'black' : 'red';
            if (!captureId || captureId[0] === color[0]) {
                throw new Error(`Invalid jump from ${from} to ${to}: no ${oppColor} piece to capture at ${capturePos}`);
            }
            captured = capturePos;
        }
        return { from, to, dx, dy, captured, positions: _positions };
    };

    async movePiece(moveStr, captureFlair) {
        const [, from, actionChar] = moveStr.match(/^([A-H][1-8])(?:([-x])([A-H][1-8]))(?:x([A-H][1-8]))*$/);
        const tos = moveStr.slice(3).split('x');
        if (!from || (actionChar === '-' && tos.length > 1)) {
            throw new Error(`Invalid move syntax: ${moveStr}`);
        }
        let _positions;
        const [, ...steps] = tos.reduce(
            ([from, ...steps], to) => {
                const step = this.validateStep(from, to, actionChar, _positions);
                _positions = step.positions;
                _positions[to] = _positions[from];
                delete _positions[from];
                if (step.captured) {
                    delete _positions[step.captured];
                }
                return [to, ...steps, step];
            },
            [from],
        );
        const action = actionChar === '-' ? 'move' : 'jump';
        const duration = action === 'move' ? 0.5 : 2;
        const timing = action === 'move' ? 'ease' : 'cubic-bezier(0.5, 0.05, 1, 0.5)';
        const fromId = this.positions[from];
        const piece = this.shadowRoot.getElementById(fromId);
        const pieceStyle = piece.style;
        for (const step of steps) {
            const { from, to, dx, dy, captured } = step;
            const animEndPromise = new Promise(resolve =>
                piece.addEventListener(
                    'animationend',
                    () => {
                        pieceStyle.gridArea = to;
                        pieceStyle.removeProperty('animation');
                        if (captured) {
                            store.dispatch.board.capture({ position: captured });
                        }
                        setTimeout(resolve, 100);
                    },
                    { once: true },
                ),
            );
            pieceStyle.setProperty('--move-dx', `${dx / (captured ? 2 : 1)}`);
            pieceStyle.setProperty('--move-dy', `${dy / (captured ? 2 : 1)}`);
            for (const attrib of captureFlair?.split(' ') ?? []) {
                piece.classList.add(attrib);
            }
            pieceStyle.animation = `${duration}s ${timing} ${action}`;
            await animEndPromise;
        }

        pieceStyle.removeProperty('--move-dx');
        pieceStyle.removeProperty('--move-dy');
        for (const attrib of captureFlair?.split(' ') ?? []) {
            piece.classList.remove(attrib);
        }
        const to = tos.at(-1);
        const color = this.positionColor(from);
        const rank = this.positionRank(from);
        store.dispatch.board.move({ from, to });
        if (rank === 'pawn' && ((to[1] === '1' && color === 'red') || (to[1] === '8' && color === 'black'))) {
            // promote
            store.dispatch.board.promote({ position: to });
        }
    }

    async play() {
        store.dispatch.board.reset();
        this.blackThoughts.reset();
        this.redThoughts.reset();
        this.playing = true;
        const colors = ['black', 'red'];
        // const apiKeys = objectFrom(colors.map(c => [c, document.querySelector(`chat-settings[side="${c}"]`).apiKey]));
        const [black, red] = [this.blackSettings, this.redSettings].map((settings, i) => {
            const { provider: providerId, model, baseUrl, stream, temperature } = settings;
            return new CheckersPlayer(colors[i], {
                apiKey: this.apiKeyByProvider(providerId),
                providerId,
                model,
                baseUrl,
                stream,
                temperature,
                dispatch: store.dispatch,
                role: colors[i],
            });
        });
        this.players = { black, red };

        this.dispatchEvent(new CustomEvent('play', { detail: { players: valuesOf(this.players) } }));
        store.dispatch.currentMatch.start({ red: this.redSettings, black: this.blackSettings });
        const result = await play(this, valuesOf(this.players));
        this.playing = false;
        store.dispatch.currentMatch.end({ result: { winner: result.winner, error: result.error } });
        const title = result.error ? 'Error!' : 'Game Over!';
        const winner = result.winner.replace(/^./, c => c.toUpperCase());
        const content = result.error ? html`<pre>Error: ${result.error}</pre>` : `${winner} wins the match!`;
        const notificationDialog = await this.notificationDialog;
        // @ts-ignore
        return notificationDialog.show({ title, content });
    }

    pause() {
        this.paused = this.blackThoughts.paused = this.redThoughts.paused = true;
        this.wait =
            this.blackThoughts.wait =
            this.redThoughts.wait =
                new Promise(resolve => {
                    this.addEventListener(
                        'resume',
                        _ => {
                            this.paused = this.blackThoughts.paused = this.redThoughts.paused = false;
                            resolve();
                        },
                        { once: true },
                    );
                });
    }

    resume() {
        this.dispatchEvent(new Event('resume'));
    }

    playDisabled() {}

    async streamThoughts(side, chunk, type) {
        const thoughts = side === 0 ? this.blackThoughts : this.redThoughts;
        await this.wait;
        await thoughts.streamMessage(chunk, type);
    }

    showThoughts(side, comment, type) {
        const thoughts = side === 0 ? this.blackThoughts : this.redThoughts;
        // @ts-ignore
        thoughts.addMessage(comment, type);
    }

    renderColLabels() {
        return columns.map(col => html`<div>${col}</div>`);
    }

    renderRowLabels() {
        return rows.map(row => html`<div>${9 - row}</div>`);
    }

    renderSquares() {
        const color = pos => (activeSquares.has(pos) ? 'black' : 'white');
        return allSquares.map(pos => html`<div class="square ${color(pos)}" style="grid-area: ${pos}"></div>`);
    }

    renderPieces() {
        const posIds = Object.entries(this.positions);
        return html`${repeat(
            posIds,
            ([_, id]) => id,
            ([pos, id]) =>
                html`<div
                    id="${id}"
                    class="piece ${this.positionColor(pos)} ${this.positionRank(pos)}"
                    style="grid-area: ${pos}"
                ></div>`,
        )}`;
    }

    renderCaptures(captures) {
        return repeat(
            captures,
            ({ piece }) => piece,
            ({ piece, color, rank }) => html`<div id="${piece}" class="piece ${color} ${rank}"></div>`,
        );
    }

    renderControls() {
        const action = this.playing ? (this.paused ? 'resume' : 'pause') : this.canPlay ? 'play' : 'playDisabled';
        const hint = this.playing
            ? this.paused
                ? 'resume'
                : 'pause'
            : this.canPlay
              ? 'play'
              : 'enter API keys to play';

        return html`
            <div class="controls">
                <button ?disabled=${!this.canPlay} @click=${_ => this[action]()}>
                    ${icon(action, 'var(--icon-size)')}
                    <div class="hint">${hint}</div>
                </button>
            </div>
        `;
    }

    renderBoard() {
        if (this.paused) {
            this.wait.then(_ => this.requestUpdate());
            return noChange;
        }
        return html`<div class="board">
            <div class="col-labels top">${this.renderColLabels()}</div>
            <div class="row-labels left">${this.renderRowLabels()}</div>
            <div class="col-labels bottom">${this.renderColLabels()}</div>
            <div class="row-labels right">${this.renderRowLabels()}</div>
            ${this.renderSquares()}${this.renderPieces()}
            <div class="captures red">${this.renderCaptures(this.redCaptures)}</div>
            <div class="captures black">${this.renderCaptures(this.blackCaptures)}</div>
        </div>`;
    }

    render() {
        return [this.renderBoard(), this.renderControls()];
    }
}

customElements.define('checker-board', Checkerboard);
