import './checkers.types.js';

/**
 * Represents a game of checkers
 */
export class Checkers {
    static EMPTY = '.';
    static BLACK_PAWN = 'b';
    static BLACK_KING = 'B';
    static RED_PAWN = 'r';
    static RED_KING = 'R';

    /** @type {Board} */
    board;
    /** @type {boolean} */
    isBlackTurn;

    /**
     * Creates a new checkers match with initial board state
     */
    constructor(blackStarts = true) {
        this.board = this.createInitialBoard();
        this.isBlackTurn = blackStarts; // black moves first?
    }

    createInitialBoard() {
        const board = Array.from({ length: 8 }, () => Array(8).fill(Checkers.EMPTY));

        // Black pawns on rows 0, 1, 2 (on dark squares)
        for (let row = 0; row < 3; row++) {
            for (let col = 0; col < 8; col++) {
                if ((row + col) % 2 !== 0) {
                    board[row][col] = Checkers.BLACK_PAWN;
                }
            }
        }
        // Red pawns on rows 5, 6, 7 (on dark squares)
        for (let row = 5; row < 8; row++) {
            for (let col = 0; col < 8; col++) {
                if ((row + col) % 2 !== 0) {
                    board[row][col] = Checkers.RED_PAWN;
                }
            }
        }

        return board;
    }

    /**
     * Generates all legal moves for the current player
     * @param {Board} [board] Optional board state to generate moves for
     * @param {boolean} [isBlackTurn] Optional turn indicator
     * @returns {Move[]}
     */
    generateAllMoves(board = this.board, isBlackTurn = this.isBlackTurn) {
        const moves = [];
        const playerPieces = isBlackTurn
            ? [Checkers.BLACK_PAWN, Checkers.BLACK_KING]
            : [Checkers.RED_PAWN, Checkers.RED_KING];

        for (let row = 0; row < 8; row++) {
            for (let col = 0; col < 8; col++) {
                const piece = board[row][col];
                if (playerPieces.includes(piece)) {
                    const pieceMoves = this.getPieceMoves(board, row, col);
                    moves.push(...pieceMoves);
                }
            }
        }

        // If there's at least one capturing move, we must filter out non-capturing moves
        const anyCapture = moves.some(m => m.captured.length > 0);
        if (anyCapture) {
            return moves.filter(m => m.captured.length > 0);
        }
        return moves;
    }

    /**
     * Gets all legal moves for a piece
     * @param {Board} board Current board state
     * @param {number} row Piece row
     * @param {number} col Piece column
     * @returns {Move[]}
     */
    getPieceMoves(board, row, col) {
        const piece = board[row][col];
        // First check if there are capturing moves
        const captures = this.findCaptures(board, row, col, piece);
        if (captures.length > 0) {
            return captures;
        }
        // Otherwise, normal moves
        return this.getNormalMoves(board, row, col, piece);
    }

    /**
     * Gets non-capturing moves for a piece
     * @param {Board} board Current board state
     * @param {number} row Piece row
     * @param {number} col Piece column
     * @param {string} piece Piece type
     * @returns {Move[]}
     */
    getNormalMoves(board, row, col, piece) {
        const directions = this.getDirectionsForPiece(piece);
        const moves = [];
        for (const dir of directions) {
            const newRow = row + dir.dr;
            const newCol = col + dir.dc;
            if (this.isOnBoard(newRow, newCol) && board[newRow][newCol] === Checkers.EMPTY) {
                moves.push({
                    from: { row, col },
                    to: { row: newRow, col: newCol },
                    captured: [],
                });
            }
        }
        return moves;
    }

    /**
     * Finds all capturing moves for a piece
     * @param {Board} board Current board state
     * @param {number} row Piece row
     * @param {number} col Piece column
     * @param {string} piece Piece type
     * @param {Position[]} [capturedSoFar] Previously captured pieces
     * @param {Position[]} [jumpsSoFar] Previously made jumps
     * @param {Position} [origFrom] Original starting position
     * @returns {Move[]}
     */
    findCaptures(board, row, col, piece, capturedSoFar = [], jumpsSoFar = [], origFrom = { row, col }) {
        const directions = this.getDirectionsForPiece(piece);
        let result = [];

        for (const dir of directions) {
            const midRow = row + dir.dr;
            const midCol = col + dir.dc;
            const jumpRow = row + 2 * dir.dr;
            const jumpCol = col + 2 * dir.dc;

            if (
                this.isOnBoard(jumpRow, jumpCol) &&
                this.isEnemyPiece(piece, board[midRow]?.[midCol]) &&
                board[jumpRow][jumpCol] === Checkers.EMPTY
            ) {
                const nextBoard = this.copyBoardState(board);
                nextBoard[row][col] = Checkers.EMPTY;
                nextBoard[midRow][midCol] = Checkers.EMPTY;

                const newCaptured = [...capturedSoFar, { row: midRow, col: midCol }];
                const newJumps = [...jumpsSoFar, { row: jumpRow, col: jumpCol }];

                const furtherJumps = this.findCaptures(
                    nextBoard,
                    jumpRow,
                    jumpCol,
                    piece,
                    newCaptured,
                    newJumps,
                    origFrom,
                );

                if (furtherJumps.length > 0) {
                    result.push(...furtherJumps);
                } else {
                    result.push({
                        from: { row: origFrom.row, col: origFrom.col },
                        to: { row: jumpRow, col: jumpCol },
                        captured: newCaptured,
                        jumps: newJumps,
                    });
                }
            }
        }

        return result;
    }

    /**
     * Gets valid move directions for a piece type
     * @param {string} piece Piece type
     * @returns {Direction[]}
     */
    getDirectionsForPiece(piece) {
        const BLACK_DIRECTIONS = [
            { dr: 1, dc: -1 },
            { dr: 1, dc: 1 },
        ];
        const RED_DIRECTIONS = [
            { dr: -1, dc: -1 },
            { dr: -1, dc: 1 },
        ];
        const KING_DIRECTIONS = [
            { dr: 1, dc: -1 },
            { dr: 1, dc: 1 },
            { dr: -1, dc: -1 },
            { dr: -1, dc: 1 },
        ];

        switch (piece) {
            case Checkers.BLACK_PAWN:
                return BLACK_DIRECTIONS;
            case Checkers.RED_PAWN:
                return RED_DIRECTIONS;
            case Checkers.BLACK_KING:
            case Checkers.RED_KING:
                return KING_DIRECTIONS;
            default:
                return [];
        }
    }

    /**
     * Checks if position is within board bounds
     * @param {number} row
     * @param {number} col
     * @returns {boolean}
     */
    isOnBoard(row, col) {
        return row >= 0 && row < 8 && col >= 0 && col < 8;
    }

    /**
     * Checks if target piece belongs to opponent
     * @param {string} currentPiece Current piece
     * @param {string} targetPiece Target piece
     * @returns {boolean}
     */
    isEnemyPiece(currentPiece, targetPiece) {
        return targetPiece !== Checkers.EMPTY && currentPiece.toLowerCase() !== targetPiece.toLowerCase();
    }

    /**
     * Promotes pawns that reach opposite end of board
     * @param {string} piece Piece type
     * @param {number} row Current row
     * @returns {string} New piece type
     */
    maybePromotePiece(piece, row) {
        if (piece === Checkers.BLACK_PAWN && row === 7) {
            return Checkers.BLACK_KING;
        }
        if (piece === Checkers.RED_PAWN && row === 0) {
            return Checkers.RED_KING;
        }
        return piece;
    }

    /**
     * Creates a copy of the board state
     * @param {Board} board Board to copy
     * @returns {Board}
     */
    copyBoardState(board) {
        return board.map(r => r.slice());
    }

    /**
     * Parses move string in algebraic notation
     * @param {string} moveStr Move in algebraic notation
     * @returns {ParsedMove}
     */
    parseMoveString(moveStr) {
        const squares = moveStr.split(/[-x]/);
        const from = this.algebraicToRowCol(squares[0]);
        const to = this.algebraicToRowCol(squares.at(-1));
        return { from, to, isCapture: moveStr.includes('x') };
    }

    /**
     * Formats a move into algebraic notation
     * @param {Move} move Move to format
     * @returns {string}
     */
    formatMove(move) {
        const fromAlg = this.rowColToAlgebraic(move.from.row, move.from.col);
        const toAlg = this.rowColToAlgebraic(move.to.row, move.to.col);

        if (!move.captured || move.captured.length === 0) {
            return `${fromAlg}-${toAlg}`;
        }

        let notation = fromAlg;
        if (move.jumps) {
            for (const jump of move.jumps) {
                notation += 'x' + this.rowColToAlgebraic(jump.row, jump.col);
            }
        } else {
            notation += 'x' + toAlg;
        }
        return notation;
    }

    /**
     * Applies a move to the current board state
     * @param {string} moveStr Move in algebraic notation
     * @returns {string[]} positions of captured pieces
     */
    applyMove(moveStr) {
        const parsed = this.parseMoveString(moveStr);
        const legalMoves = this.generateAllMoves();
        const move = legalMoves.find(move => {
            const startMatches = move.from.row === parsed.from.row && move.from.col === parsed.from.col;
            const endMatches = move.to.row === parsed.to.row && move.to.col === parsed.to.col;

            if (parsed.isCapture) {
                return startMatches && endMatches && move.captured.length > 0;
            }
            return startMatches && endMatches;
        });

        if (!move) {
            throw new Error(`Illegal move: ${moveStr}`);
        }

        const piece = this.board[move.from.row][move.from.col];
        this.board[move.from.row][move.from.col] = Checkers.EMPTY;
        for (const cap of move.captured) {
            this.board[cap.row][cap.col] = Checkers.EMPTY;
        }
        const newPiece = this.maybePromotePiece(piece, move.to.row);
        this.board[move.to.row][move.to.col] = newPiece;

        this.isBlackTurn = !this.isBlackTurn;
        return move.captured.map(c => this.rowColToAlgebraic(c.row, c.col));
    }

    /**
     * Checks if game is over (no legal moves)
     * @returns {boolean}
     */
    isGameOver() {
        const legalMoves = this.generateAllMoves(this.board, this.isBlackTurn);
        return legalMoves.length === 0;
    }

    /**
     * Converts algebraic notation to row/col position
     * @param {string} algebraic Position in algebraic notation
     * @returns {Position}
     */
    algebraicToRowCol(algebraic) {
        const colLetter = algebraic[0].toUpperCase();
        const rowNumber = parseInt(algebraic.slice(1), 10);
        const col = colLetter.charCodeAt(0) - 'A'.charCodeAt(0);
        const row = rowNumber - 1;
        return { row, col };
    }

    /**
     * Converts row/col position to algebraic notation
     * @param {number} row
     * @param {number} col
     * @returns {string}
     */
    rowColToAlgebraic(row, col) {
        const colLetter = String.fromCharCode('A'.charCodeAt(0) + col);
        const rowNumber = row + 1;
        return `${colLetter}${rowNumber}`;
    }

    /**
     * Returns string representation of current board state
     * @returns {string}
     */
    printBoard() {
        let output = ['  ABCDEFGH'];
        for (let row = 7; row >= 0; row--) {
            output.push(`${row + 1} ${this.board[row].join('')}`);
        }
        return output.join('\n');
    }

    async play(black, red, board) {
        this.isBlackTurn = true;
        while (!this.isGameOver()) {
            const player = this.isBlackTurn ? black : red;
            const move = await player.makeMove(this.board, this.isBlackTurn);
            this.applyMove(move);
        }
    }
}
