const renderValue = v => (v instanceof RegExp ? v.source : Array.isArray(v) ? v.map(renderValue) : v);

export const regex = (strings, ...values) => {
    // Extract flags if passed as the last argument
    let flags = typeof values.at(-1) === 'string' && /^\/[gimsuydx]*$/.test(values.at(-1)) ? values.pop() : '';

    values = values.map(renderValue);

    // Join template literal parts and interpolate values
    let pattern = strings.raw.flatMap((str, i) => [str, ...(values[i] ?? '')]).join('');

    // Remove comments (# ...)
    pattern = pattern.replace(/#[^\n]*/g, '');

    // Remove unescaped whitespace (except inside character classes `[...]`)
    pattern = pattern.replace(/(?<escape>\\.)|(?<charClass>\[.*?(?<!\\)])|(\s+)/g, '$<escape>$<charClass>');

    return new RegExp(pattern, flags?.slice(1));
};

regex/*regexp*/ `
    (\\.)               | # first pull escape sequences out of the string
    (\[.*?(?<!\\)\])    | # then charClasses
    (\#[^\n]*)          | # then comments
    (\s+)                 # then whitespace
    ${'/gs'}
`;

const notAfter = (ss, vs) => regex`(?<!${ss.raw.flatMap((s, i) => [s, vs?.[i] ?? '']).join('')})`;
