import { css } from 'lit';
import pieceStyles from './piece.css.js';

export default css`
    :host {
        --piece-z: 13px;
        --hero-size: calc(var(--aside-width) / 3.5);
        --square-side: calc(var(--hero-size) * 0.6 / 0.86);
        perspective: 800px;
    }
    .hero {
        position: absolute;
        top: 0;
        right: 0;
        width: var(--hero-size);
        aspect-ratio: 1;
        perspective-origin: calc(100% - var(--hero-size) / 2) calc(var(--hero-size) / 2);
        z-index: -1;
    }

    .hero .piece {
        height: 60%;
        width: 60%;
        aspect-ratio: 1;
        position: absolute;
        right: 5px;
        top: 5px;
        opacity: 1;
        transform: rotateY(0deg);
        transform-style: preserve-3d;
        transform-origin: center center;
    }
    .hero .piece.red.king::before {
        transform: rotate(0) translateZ(calc(2 * var(--piece-z)));
    }
    form:not(:focus-within):hover + .hero > .piece {
        animation: spin 1.25s cubic-bezier(0, 0.46, 0.36, 0.92) 100ms forwards;
    }

    @keyframes spin {
        15% {
            transform: translateX(60px) rotateY(1200deg);
        }
        to {
            transform: translateX(0px) rotateY(4320deg);
        }
    }

    .hero .piece,
    .hero .piece::after,
    .hero .piece::before {
        border-style: inset;
        box-shadow:
            3px 3px 6px 1px #0004,
            -1px -1px 2px 1px #fff4;
    }

    form {
        /* border: 2px solid transparent; */
        position: relative;
        display: grid;
        grid-template:
            'L1 S1 S1' auto
            'L3 S2 S2' auto
            'L2 I1 I1' auto
            'L2 C1 C2' auto / min-content 1fr 1fr;
        align-items: baseline;
        width: 75%;
        transition:
            width 333ms ease-in-out,
            background-color 1500ms ease-in-out;
        overflow: hidden;
        text-overflow: ellipsis;
        background-color: #0000;
    }
    form:hover,
    form:has(*:focus-visible),
    form:has(*:active) {
        width: 100%;
        background-color: rgba(from var(--bg-chat) r g b / 0.7);

        .secondary {
            opacity: 1;
            height: 1.5em;
            transition:
                height 333ms ease-in-out,
                opacity 333ms ease-in-out;
        }
        label.secondary {
            margin: 3px;
        }
        input.secondary {
            border: 1px solid var(--border-color);
            margin: 0;
        }

        select,
        input:not([type='checkbox']) {
            appearance: revert;
            background-color: rgba(from var(--input-bg) r g b / 0.5);
            border: 1px solid var(--border-color);
            color: var(--text-main);
            margin-inline: 0px;
            padding-left: 1px;
        }

        input::placeholder {
            opacity: 1;
        }
    }
    label {
        color: var(--accent);
        margin: 3px;
        text-align: right;
        line-height: 1.6;
        white-space: nowrap;
    }
    label[disabled],
    input[disabled],
    select[disabled],
    fieldset[disabled] {
        display: none;
    }
    label.labelFollowsInput {
        text-align: left;
        font-size: 0.8em;
    }
    @media (max-width: 1280px), (max-width: 640px) {
        :host {
            --hero-size: calc(var(--aside-width) / 4.5);
        }
        form {
            grid-template:
                'L1'
                'S1'
                'L3'
                'S2'
                'L2'
                'I1'
                'C1'
                'C2' / 1fr;
        }
        label {
            text-align: left;
        }
    }
    @media (max-width: 800px) {
        :host {
            --hero-size: calc(var(--aside-width) / 3.5);
        }
        form {
            grid-template:
                'L1 S1 S1' auto
                'L3 S2 S2' auto
                'L2 I1 I1' auto
                'L2 C1 C2' auto / min-content 1fr 1fr;
        }
        label {
            text-align: right;
        }
    }

    @media (max-width: 640px) {
        :host {
            --hero-size: calc(var(--aside-width) / 4.5);
        }
        form {
            grid-template:
                'L1'
                'S1'
                'L3'
                'S2'
                'L2'
                'I1'
                'C1'
                'C2' / 1fr;
        }
        label {
            text-align: left;
        }
    }

    .secondary {
        overflow: hidden;
        height: 0;
        opacity: 0;
        margin: 0;
        margin-top: -6px; /* fix table row won't collapse */
        padding: 0;
        border: 0;
        transition:
            margin 333ms ease-in-out,
            height 333ms ease-in-out,
            opacity 200ms ease-in-out;
    }

    input[type='checkbox']:checked {
        background-color: var(--accent);
        color: black;
    }
    input[type='checkbox'] {
        appearance: none;
        background-color: #ddd;
        border-radius: 3px;
        border: 1px solid var(--border-color);
        vertical-align: text-top;
        margin-right: 2px;
        position: relative;
        display: inline-block;
        height: 0.8em;
        aspect-ratio: 1;
    }

    input[type='checkbox']:checked::after {
        content: '✓';
        position: absolute;
        font-family: system-ui;
        font-size: 0.8em;
        font-weight: bold;
        text-align: center;
        vertical-align: middle;
        line-height: 0.8em;
    }

    select,
    input:not([type='checkbox']) {
        appearance: none;
        background-color: transparent;
        color: var(--text-main);
        border: 1px solid transparent;
        border-radius: 4px;
        vertical-align: baseline;
        font-size: 0.9em;
        padding-left: 5px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: calc(100% - 1px);
    }
    input:not([type='checkbox']).secondary {
        border-color: transparent;
    }
    input::placeholder {
        opacity: 0;
        transition: opacity 333ms ease-in-out;
    }
    .labelFollowsInput {
        display: inline-block;
    }

    input[name='apiKey'],
    input[name='baseUrl'] {
        width: calc(100% - 10px);
    }

    .dragover {
        border-color: var(--accent);
        border-style: dashed;
        border-width: 2px;
    }

    ${pieceStyles}
`;
