import { LitElement, html } from 'lit';
import { live } from 'lit/directives/live.js';
import { createRef, ref } from 'lit/directives/ref.js';
import chatProviders from './chatProviders.js';
import chatSettingsCss from './chatSettings.css.js';
import { selection, store } from './store.js';
import { connect } from 'pwa-helpers';
import { regex } from './regex.js';
import { entriesOf, keysOf, mapObject, objectFrom } from './util.js';

/**
 * @type ChatSettings
 * @property {string} providerId
 * @property {Object} provider
 * @property {string} apiKey
 * @property {boolean} saveApiKeys
 * @property {string} model
 * @property {string} baseUrl
 */
export class ChatSettings extends connect(store)(LitElement) {
    static styles = chatSettingsCss;

    static properties = {
        providerId: { type: String, attribute: 'provider', reflect: true },
        provider: { type: Object, attribute: false },
        apiKey: { type: String },
        saveApiKeys: { type: Boolean },
        showApiKey: { type: Boolean },
        model: { type: String, attribute: true, reflect: true },
        baseUrl: { type: String, attribute: true },
        side: { type: String, attribute: true },
        store: { type: Object },
    };

    constructor() {
        super();
        this.showApiKey = false;
        this.formRef = createRef();
        this.side = '';
        this.store = null;
        customElements.whenDefined('modal-dialog').then(_ => (this.modal = document.querySelector('modal-dialog')));
        customElements
            .whenDefined('notification-dialog')
            .then(_ => (this.notify = document.querySelector('notification-dialog')));
    }

    stateChanged(state) {
        const selectors = selection(state, { color: this.side });
        this.providerId = selectors.provider(this.side);
        this.provider = chatProviders[this.providerId];
        this.model = selectors.model(this.side);
        this.saveApiKeys = selectors.saveApiKeys;
        this.apiKey = selectors.apiKeyByProvider(this.providerId);
        this.baseUrl = selectors.baseUrl(this.side);
    }

    firstUpdated() {
        this.providerId ??= 'openai';
        this.provider = chatProviders[this.providerId];
        this.baseUrl = this.provider.baseUrl;
        const form = this.formRef.value;
        form.addEventListener('dragover', e => this.dragover(e));
        form.addEventListener('dragleave', e => this.dragleave(e));
        form.addEventListener('drop', e => this.drop(e));

        form.addEventListener('beforeinput', event => {
            if (event.inputType === 'insertFromPaste') {
                const pastedData = event.dataTransfer?.getData('text/plain') || event.data;
                if (pastedData.match(/\n/)) {
                    this.processEnvText(pastedData);
                } else {
                    event.target.value = pastedData;
                }
                event.preventDefault();
            }
        });
    }

    dragover(event) {
        event.preventDefault();
        event.stopPropagation();
        event.currentTarget.classList.add('dragover');
    }

    dragleave(event) {
        event.stopPropagation();
        event.currentTarget.classList.remove('dragover');
    }

    drop(event) {
        if (event.dataTransfer.files?.length) {
            const file = event.dataTransfer.files[0];
            const reader = new FileReader();
            reader.onload = e => {
                const text = /** @type string */ (e.target.result);
                this.processEnvText(text);
            };
            reader.readAsText(file);
            event.preventDefault();
            event.stopPropagation();
        }
        event.currentTarget.classList.remove('dragover');
    }

    processEnvText(text) {
        const apiKeyNames = mapObject(chatProviders, ({ apiKeyName }) => apiKeyName);
        const envRegex = regex`${entriesOf(apiKeyNames)
            .filter(([k, v]) => !!v)
            .map(([provider, keyName]) => regex/* regexp */ `^${keyName}=(?<${provider}>[^\n]*)$`)
            .flatMap((p, i, ps) => [p, '|'])
            .slice(0, -1)}${'/gm'}`;
        const providerKeys = objectFrom(
            [...text.matchAll(envRegex)].flatMap(m => (m.groups ? entriesOf(m.groups).filter(([k, v]) => !!v) : [])),
        );

        store.dispatch.apiKeys.set(providerKeys);
        const numKeys = keysOf(providerKeys).length;
        if (numKeys) {
            const singular = numKeys === 1;
            this.notify.show({
                title: numKeys + ' API keys found',
                content: html`API key${singular ? '' : 's'} have been set for: ${keysOf(providerKeys).join(', ')}.`,
                autoDismiss: 5000,
            });
        }
    }

    static modals = {
        saveApiKeys: {
            title: 'Save API Keys Warning',
            content: html`This feature will save your API keys in local storage. Do not use this feature on a shared
            computer.`,
            actions: [
                html`<button class="primary" value="confirmed">Confirm</button>`,
                html`<button value="canceled">Cancel</button>`,
            ],
        },
        expandedSettingsForm: {
            title: 'Settings',
            content: html``,
            actions: [
                html`<button class="primary" value="saved">Save</button>`,
                html`<button value="canceled">Cancel</button>`,
            ],
        },
    };

    async saveApiKeysChanged(event) {
        event.stopPropagation();
        const checkbox = event.target;
        if (checkbox.checked) {
            const confirmed = (await this.modal.show(ChatSettings.modals.saveApiKeys)) === 'confirmed';
            if (confirmed) {
                store.dispatch.settings.setSaveApiKeys({ save: true });
            }
            this.requestUpdate('saveApiKeys', !confirmed);
        } else {
            store.dispatch.settings.setSaveApiKeys({ save: false });
        }
    }

    showApiKeyChanged(event) {
        this.showApiKey = event.target.checked;
        this.requestUpdate('showApiKey', !this.showApiKey);
        event.stopPropagation();
    }

    render() {
        return html`
            <form ${ref(this.formRef)}>
                <label style="grid-area: L1" for="${this.side}_apiProvider">Provider</label>
                <select
                    id="${this.side}_apiProvider"
                    value="${this.providerId}"
                    name="apiProvider"
                    style="grid-area: S1"
                    @change=${e =>
                        store.dispatch.settings.setPlayerProvider({ color: this.side, provider: e.target.value })}
                >
                    ${Object.entries(chatProviders).map(
                        ([id, provider]) =>
                            html`<option ?selected=${this.providerId === id} value="${id}">${provider.name}</option>`,
                    )}
                </select>

                <label for="${this.side}_aiModel" style="grid-area: L3">Model</label>
                <select
                    id="${this.side}_aiModel"
                    style="grid-area: S2"
                    name="aiModel"
                    @change=${e => store.dispatch.settings.setPlayerModel({ color: this.side, model: e.target.value })}
                    value="${this.model}"
                >
                    ${this.provider?.models.map(
                        model => html`<option ?selected=${this.model === model} value="${model}">${model}</option>`,
                    )}
                </select>

                <label
                    for="${this.side}_apiKey"
                    style="grid-area: L2"
                    class="secondary"
                    ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                    >API Key</label
                >
                <input
                    class="secondary"
                    style="grid-area: I1"
                    type="${this.showApiKey ? 'text' : 'password'}"
                    name="apiKey"
                    id="${this.side}_apiKeys"
                    .value="${this.apiKey ?? ''}"
                    placeholder="Enter API key for ${this.provider?.name ?? ''} ..."
                    ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                    @change="${e => store.dispatch.apiKeys.set({ provider: this.providerId, apiKey: e.target.value })}"
                    title="${this.provider?.name
                        ? `Enter your ${this.provider?.name} API key here, or drop your .env file here and your API keys will be extracted.`
                        : ''}"
                /><label
                    class="labelFollowsInput secondary"
                    style="grid-area: C1"
                    ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                    for="${this.side}_saveApiKeys"
                    ><input
                        id="${this.side}_saveApiKeys"
                        name="saveApiKeys"
                        type="checkbox"
                        ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                        .checked=${live(this.saveApiKeys)}
                        @change=${this.saveApiKeysChanged}
                    />Save keys</label
                ><label
                    class="labelFollowsInput secondary"
                    style="grid-area: C2"
                    ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                    for="${this.side}_showApiKey"
                    ><input
                        id="${this.side}_showApiKey"
                        name="showApiKey"
                        type="checkbox"
                        ?disabled=${!('apiKeyName' in (this.provider ?? {}))}
                        .checked=${live(this.showApiKey)}
                        @change=${this.showApiKeyChanged}
                    />Show key</label
                >
                <label
                    for="${this.side}_baseUrl"
                    class="secondary"
                    style="grid-area: L2"
                    ?disabled=${!this.provider?.configurableUrl}
                    >Base URL</label
                >
                <input
                    id="${this.side}_baseUrl"
                    name="baseUrl"
                    class="secondary"
                    style="grid-area: I1"
                    .value="${this.baseUrl ?? this.provider?.baseUrl}"
                    placeholder="Enter Base URL..."
                    ?disabled=${!this.provider?.configurableUrl}
                    @change=${e =>
                        store.dispatch.settings.setPlayerModel({ color: this.side, baseUrl: e.target.value })}
                />
            </form>
            <div class="hero">
                <div class="piece ${this.side} pawn"></div>
            </div>
        `;
    }
}

if (!customElements.get('chat-settings')) {
    customElements.define('chat-settings', ChatSettings);
}
