export const { entries: entriesOf, fromEntries: objectFrom, keys: keysOf, values: valuesOf } = Object;
export const mapObject = (obj, fn) => objectFrom(entriesOf(obj).map(([key, val]) => [key, fn(val, key, obj)]));
export const filterObject = (obj, fn) => objectFrom(entriesOf(obj).filter(([key, val]) => fn(val, key, obj)));
export const flatMapObject = (obj, fn) => objectFrom(entriesOf(obj).flatMap(([key, val]) => fn(val, key, obj)));

export const sumOf = (os, acc) => {
    switch (typeof os) {
        case 'number':
            return os + (acc ?? 0);

        case 'object':
            return (Array.isArray(os) ? os : [os]).reduce((sums, o) => {
                return typeof o === 'number'
                    ? o + (sums ?? 0)
                    : { ...(sums ?? {}), ...mapObject(o, (val, key) => sumOf(val, (sums ?? {})[key])) };
            }, acc);
    }
};

export const keyMerge = (ds, keys) =>
    mapObject(keys, froms =>
        ds.flatMap(d => froms.flatMap(from => (from in d ? d[from] : []))).reduce((acc, d) => acc + d, 0),
    );

export const assert = (condition, message) => {
    condition || console.error(message);
};
