import { ChatClient } from './chatClient.js';
import { store } from './store.js';

export class CheckersPlayer extends ChatClient {
    side;

    constructor(side, chatClientOptions) {
        super(chatClientOptions);
        this.side = side;
        this.usage = { input_tokens: 0, output_tokens: 0 };
    }

    /**
     * Gets JSON schema for valid moves
     * @returns {Object}
     */
    getMoveSchema(availableMoves = []) {
        return {
            name: 'CheckersMove',
            description: 'Take your turn at checkers',
            strict: true,
            schema: {
                type: 'object',
                // required: ['move'],
                required: ['comment', 'move', 'captureFlair'],
                // required: ['think', 'comment', 'move'],
                properties: {
                    captureFlair: {
                        enum: [
                            'jump',
                            'flip',
                            'spiral',
                            'back flip',
                            'double flip',
                            'triple flip',
                            'double back flip',
                            'triple back flip',
                            'double spiral',
                            'triple spiral',
                        ],
                        type: 'string',
                    },
                    // think: {
                    //     type: 'string',
                    // },
                    comment: {
                        type: 'string',
                    },
                    move: {
                        enum: availableMoves,
                        type: 'string',
                    },
                },
                additionalProperties: false,
            },
        };
    }

    async sendMessage(schema, message, chunkCallback) {
        this.config.responseFormat = {
            type: 'json_schema',
            json_schema: schema,
        };
        const response = await super.sendMessage(message, chunkCallback);
        response.content = JSON.parse(response.content);
        const { move, captures } = response.content;
        const request = this.conversation.findLast(({ role }) => role === 'user');
        store.dispatch.currentMatch.turn({
            turn: { date: Date.now(), color: this.side, move, captures, request, response },
        });
        return response;
    }
}
