import ChatProviders from './chatProviders.js';
import { parseEventStream } from './eventStream.js';
import { keyMerge, sumOf } from './util.js';

/**
 * @typedef {Object} ChatClientOptions
 * @property {string} [apiKey] - API key for authentication
 * @property {string} [providerId='openai'] - API provider ('openai', 'anthropic', or custom)
 * @property {string} [baseUrl] - Base URL for API requests
 * @property {string} [model] - Model identifier to use
 * @property {number} [maxTokens=2048] - Maximum tokens in response
 * @property {number} [temperature=1] - Temperature for response generation
 * @property {boolean} [stream=false] - stream response using Server Sent Events
 * @property {string} [responseFormat] - Response format
 * @property {string} [anthropicVersion='2023-06-01'] - Anthropic API version
 * @property {string} [role]
 * @property {object} [dispatch] - Dispatch function for messages
 *
 */

/**
 * @typedef {Object} Message
 * @property {string} role - Role of the message sender ('user' or 'assistant')
 * @property {string} content - Content of the message
 */

/**3
 */
export class ChatClient extends EventTarget {
    /**
     * Creates a new ChatClient instance
     * @param {ChatClientOptions} options - Configuration options
     */
    constructor(options = {}) {
        super();
        this.config = {
            apiKey: options.apiKey,
            provider: options.providerId?.toLowerCase() || 'openai',
            model: options.model,
            baseUrl: options.baseUrl,
            maxTokens: options.maxTokens,
            temperature: options.temperature || 1,
            stream: options.stream || false,
            anthropicVersion: options.anthropicVersion || '2023-06-01',
            role: options.role,
        };
        this.dispatch = options.dispatch;

        const provider = ChatProviders[this.config.provider];
        if (!provider) {
            throw new Error(`Unknown provider: ${this.config.provider}`);
        }

        this.handler = new provider.handler(this.config);
        this.url = this.handler.formatUrl(`${options.baseUrl ?? provider.baseUrl}`);
        this.conversation = [];
        this.model = this.config.model;
        this.usage = { input_tokens: 0, output_tokens: 0 };
    }

    accumulateUsage(message) {
        const tokenFields = {
            input_tokens: ['input_tokens', 'prompt_tokens'],
            output_tokens: ['output_tokens', 'completion_tokens'],
        };
        const messageUsage = [];
        if (message.metadata?.usage) {
            messageUsage.push(message.metadata.usage);
        } else {
            messageUsage.push(...(message.metadata?.streamData?.flatMap(d => d.usage ?? d.message?.usage ?? []) ?? []));
        }
        this.usage = sumOf([keyMerge(messageUsage, tokenFields)], this.usage);
        this.dispatchEvent(new CustomEvent('ChatClient:usage', { detail: this.usage }));
    }

    /**
     * Add a message to the conversation history
     * @param {string} role - Role of the message sender
     * @param {string} content - Content of the message
     * @param {Object} message - Full message object
     * @param {Object} [metadata] - Additional message metadata
     */
    addMessage(role, content, message, metadata = {}) {
        this.conversation.push({ role, content, message, metadata });
        this.accumulateUsage({ metadata });
    }

    /**
     * Clear the conversation history
     */
    clearConversation() {
        this.conversation = [];
    }

    /**
     * Get the current conversation history
     * @returns {Message[]} Array of conversation messages
     */
    getConversation() {
        return [...this.conversation];
    }

    /**
     * Send a message and receive a streaming response
     * @param {string} content - Message to send
     * @param {function} [onProgress] - Callback for handling streaming updates
     * @returns {Promise<object>} Complete response object
     */
    async sendMessage(content, onProgress = () => {}) {
        try {
            onProgress('');
            const context = this.getConversation();
            if (context.length > 8) {
                context.splice(4, context.length - 8);
            }
            context.push({ role: 'user', content });
            const request = this.handler.createRequestPayload(context);
            this.addMessage('user', content, request, {});
            const body = JSON.stringify(request);
            const response = await fetch(this.url, {
                method: 'POST',
                headers: this.handler.getHeaders(),
                body,
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`API request failed: ${response.status} - ${errorText}`);
            }

            if (!request.stream) {
                const responseJson = await response.json();
                const { content, message, usage } = this.handler.handleResponseJson(responseJson);
                this.addMessage('assistant', content, message, { usage });
                return { content, message, usage };
            }

            if (!response.body) {
                throw new Error('ReadableStream not supported');
            }

            const reader = response.body.getReader();
            const decoder = new TextDecoder();
            let accumulatedContent = '';
            const streamData = [];
            let accumulatedMessage = {};
            for await (const event of parseEventStream(reader, decoder)) {
                const { partialReasoningContent, partialContent, parsed, message } = this.handler.parseStreamChunk(
                    event,
                    accumulatedMessage,
                );
                if (partialContent) {
                    accumulatedContent += partialContent;
                    await onProgress(partialContent, 'comment_part');
                } else if (partialReasoningContent) {
                    await onProgress(partialReasoningContent, 'reasoning_part');
                }
                accumulatedMessage = message;
                streamData.push(parsed);
            }
            this.addMessage('assistant', accumulatedContent, accumulatedMessage, { streamData });
            const result = {
                content: accumulatedContent,
                message: accumulatedMessage,
                usage: accumulatedMessage.usage,
            };
            return result;
        } catch (error) {
            console.error('Error in chat completion:', error);
            throw error;
        }
    }

    /**
     * Update client options
     * @param {Partial<ChatClientOptions>} options - New options to set
     */
    setOptions(options = {}) {
        Object.assign(this.config, options);
        const provider = ChatProviders[this.config.provider];
        if (provider) {
            this.url = provider.url;
            this.handler = new provider.handler(this.config);
        }
    }
}
